import { ChatSDKError } from '../error/ChatSDKError';
import { ERROR_AUTHORIZE_HOSTNAME_NOT_SET } from '../error/errorMessages';
import { SDKVariableStorage } from '../variables/VariableStorage';

export function getAuthorizeUrl(storageFallback: boolean): string {
  if (!SDKVariableStorage.ENDPOINT_AUTHORIZE) {
    throw new ChatSDKError(ERROR_AUTHORIZE_HOSTNAME_NOT_SET);
  }

  const authorizeUrl = new URL(SDKVariableStorage.ENDPOINT_AUTHORIZE);
  authorizeUrl.pathname = '/oauth/token';
  authorizeUrl.searchParams.append(
    'brandId',
    String(SDKVariableStorage.BRAND_ID),
  );
  authorizeUrl.searchParams.append(
    'channelId',
    SDKVariableStorage.CHANNEL_ID ?? '',
  );
  authorizeUrl.searchParams.append(
    'visitorId',
    SDKVariableStorage.VISITOR_ID ?? '',
  );

  if (storageFallback) {
    authorizeUrl.searchParams.append('storageFallback', '1');
  }

  return authorizeUrl.toString();
}
