import { isUndefined } from '../utils/is';

import { CacheStorageError } from './CacheStorageError';

export interface ICacheStorage {
  getItem<T = unknown>(key: string): T | null;
  removeItem(key: string): void;
  setItem(key: string, data: unknown, ttl: number): void;
}

export interface CachedStorageItem {
  data: unknown;
  expiresAt: number;
}

/**
 * Cache storage wrapper
 * - A simple wrapper around the Storage API with a TTL (Time To Live) per item.
 * - Uses JSON.stringify and JSON.parse to serialize and deserialize data.
 * - Uses Date.getTime() to calculate the expiration time.
 *
 * @param storage - The storage instance (e.g., localStorage or sessionStorage).
 */
export class CacheStorage implements ICacheStorage {
  #storage: Storage;

  /**
   * Create a new CacheStorage instance
   * @param storage - storage instance
   * @throws CacheStorageError
   */
  constructor(storage: Storage) {
    if (
      !storage ||
      !storage.getItem ||
      !storage.setItem ||
      !storage.removeItem
    ) {
      throw new CacheStorageError('CacheStorage: Storage is required');
    }
    this.#storage = storage;
  }

  /**
   * Get an item from the storage if not expired
   * @param key - key
   */
  public getItem<T = unknown>(key: string): T | null {
    const item = this.#storage.getItem(key);

    if (!item) {
      return null;
    }

    const { data = undefined, expiresAt = 0 }: CachedStorageItem =
      JSON.parse(item);
    const isExpired = expiresAt < new Date().getTime();

    if (isExpired || isUndefined(data)) {
      this.#storage.removeItem(key);

      return null;
    }

    return data as T;
  }

  /**
   * Remove an item from the storage
   * @param key - key
   */
  public removeItem(key: string): void {
    this.#storage.removeItem(key);
  }

  /**
   * Save an item in the storage with a ttl
   * @param key - key
   * @param data - data to store
   * @param ttl - time to live in milliseconds
   */
  public setItem(key: string, data: unknown, ttl: number): void {
    const item: CachedStorageItem = {
      data,
      expiresAt: new Date().getTime() + ttl,
    };

    this.#storage.setItem(key, JSON.stringify(item));
  }
}
