import { isNil } from '../utils/is';

import { ChatEventData } from './ChatEvent';

export type EventMiddleware = (
  eventData: ChatEventData,
) => ChatEventData | null;

export class ChatEventMiddleware {
  middlewares: Array<EventMiddleware> = [];

  register(middleware: EventMiddleware): void {
    this.middlewares.push(middleware);
  }

  process(event: ChatEventData | null): ChatEventData | null {
    if (isNil(event)) {
      return null;
    }

    let processedEvent: ChatEventData | null = event;

    for (const middleware of this.middlewares) {
      if (processedEvent === null) {
        return null;
      }
      processedEvent = middleware(processedEvent);
    }

    return processedEvent;
  }
}
