import { sendChatEvent } from '../event/sendChatEvent';
import { createMessageData } from '../message/createMessageData';
import { getPostbackMessageContent } from '../message/getPostbackMessageContent';
import { getTextMessageContent } from '../message/getTextMessageContent';
import {
  isLoadMoreMessagesEventData,
  MoreMessagesLoadedEvent,
} from '../message/loadMoreMessagesEventData';
import { createLoadMoreMessagesPayloadData } from '../message/loadMoreMessagesPayload';
import { MessageSuccessEventData } from '../message/MessageSuccessEventData';
import { sendMessage } from '../message/sendMessage';
import { createSendMessagePreviewPayloadData } from '../message/sendMessagePreviewPayload';
import { sendOutboundMessage } from '../message/sendOutboundMessage';
import { getThirdPartyToken } from '../securedSession/thirdPartyTokenStorage';
import { WebSocketClient } from '../sockets/WebSocketClient';
import { AbortablePromise } from '../utils/AbortablePromise';
import { generateId } from '../utils/generateId';
import { isEmpty, isUndefined } from '../utils/is';
import { SDKVariableStorage } from '../variables/VariableStorage';

import { createArchiveThreadEventPayloadData } from './archiveThread';
import { ArchiveThreadFailedError } from './ArchiveThreadFailedError';
import { assertNonEmptyTextMessageContent } from './assertNonEmptyTextMessageContent';
import { GetMetadataFailedError } from './GetMetadataFailedError';
import {
  isLoadMetadataSuccessPayload,
  LoadThreadMetadataChatEvent,
} from './isLoadMetadataSuccessPayload';
import { isRecoverSuccessPayload } from './isRecoverSuccessPayload';
import { isThreadArchivedSuccessPayload } from './isThreadArchivedSuccessPayload';
import { createLastMessageSeenPayloadData } from './lastMessageSeenPayload';
import { LoadMoreMessagesFailedError } from './LoadMoreMessagesFailedError';
import { createLoadThreadMetadataEventPayloadData } from './metadata';
import { createRecoverThreadPayloadData } from './recoverThreadPayload';
import { SendMessageOptions } from './SendMessageOptions';
import { createSetThreadCustomFieldsPayloadData } from './setThreadCustomFieldsPayload';
import { SetThreadNameFailedError } from './SetThreadNameFailedError';
import { filterCurrentThreadEventHandler } from './threadEventHandler';
import { ThreadRecoveredData } from './ThreadRecoveredData';
import { ThreadRecoverFailedError } from './ThreadRecoverFailedError';
import { createSendTranscriptPayloadData } from './transcript';
import {
  createStartTypingEventPayloadData,
  createStopTypingEventPayloadData,
} from './typingEvents';
import {
  createUpdateThreadPayloadData,
  isUpdateThreadSuccess,
} from './updateThread';

export class Thread {
  public idOnExternalPlatform: ThreadIdOnExternalPlatform;

  protected _websocketClient: WebSocketClient;
  protected _existsOnPlatform: boolean = false;

  protected _messageEmitter: IChatEventTarget;
  protected _typingTimeoutID: ReturnType<typeof setTimeout> | undefined =
    undefined;
  protected _isAuthorizationEnabled: boolean = false;
  protected _customer: Customer | null;
  protected _customFields: CustomFieldsMap = new Map();

  private _typingForPreviewTimeoutID: ReturnType<typeof setTimeout> | undefined;
  private _typingPreviewText: string = '';

  constructor(
    idOnExternalPlatform: ThreadIdOnExternalPlatform,
    websocketClient: WebSocketClient,
    messageEmitter: IChatEventTarget,
    customer: Customer | null,
    customFields: CustomFieldsObject = {},
    isAuthorizationEnabled: boolean = false,
  ) {
    this.idOnExternalPlatform = idOnExternalPlatform;
    this._websocketClient = websocketClient;
    this._messageEmitter = messageEmitter;
    this._customer = customer;
    this._isAuthorizationEnabled = isAuthorizationEnabled;
    this._existsOnPlatform = false;

    setCustomFields(this._customFields, customFields);
    this._registerEventHandlers();
  }

  /**
   * Recover existing chat
   * @returns AbortablePromise<ThreadRecoveredData>
   * @throws ThreadRecoverFailedError
   *  * This exception is thrown when the recover fails or the thread does not exist.
   */
  public recover(): AbortablePromise<ThreadRecoveredData> {
    return new AbortablePromise(async (resolve, reject) => {
      const response = await sendChatEvent(
        createRecoverThreadPayloadData(this.idOnExternalPlatform),
        this._websocketClient,
      );

      if (isRecoverSuccessPayload(response)) {
        resolve(response.data);
      } else {
        reject(new ThreadRecoverFailedError('Thread not found', { response }));
      }
    });
  }

  /**
   * Send message
   * @param messageData - message data
   * @throws SendMessageFailedError
   *  * This exception is thrown when a message fails to send. The error can contain (`error.data`) a response from the backend with details.
   */
  public async sendMessage(
    messageData: SendMessageEventData,
  ): Promise<MessageSuccessEventData> {
    assertNonEmptyTextMessageContent(messageData);

    const messageDataWithCustomFields =
      this._mergeCustomFieldsAndAccessTokenWithMessageData(messageData, false);

    return sendMessage(messageDataWithCustomFields, this._websocketClient);
  }

  /**
   * Send text message
   * @param messageText - text of message
   * @param options - options
   */
  public async sendTextMessage(
    messageText: string,
    options: SendMessageOptions = {},
  ): Promise<MessageSuccessEventData> {
    const {
      messageId = generateId(),
      browserFingerprint = getBrowserFingerprint(),
    } = options;

    const textMessageContent = getTextMessageContent(messageText);
    const messageData = createMessageData(
      textMessageContent,
      messageId,
      this.idOnExternalPlatform,
      browserFingerprint,
    );

    return this.sendMessage(messageData);
  }

  /**
   * Send postback message
   * @param postback - postback
   * @param messageText - text of message
   * @param options - options
   */
  public async sendPostbackMessage(
    postback: string,
    messageText: string,
    options: SendMessageOptions = {},
  ): Promise<MessageSuccessEventData> {
    const {
      messageId = generateId(),
      browserFingerprint = getBrowserFingerprint(),
    } = options;

    const postbackMessageContent = getPostbackMessageContent(
      postback,
      messageText,
    );
    const messageData = createMessageData(
      postbackMessageContent,
      messageId,
      this.idOnExternalPlatform,
      browserFingerprint,
    );

    return this.sendMessage(messageData);
  }

  /**
   * Send Outbound Message
   * @param messageData - message data
   * @throws SendMessageFailedError
   *  * This exception is thrown when a message fails to send. The error contains (`error.data`) a response from the backend with details.
   */
  public async sendOutboundMessage(
    messageData: SendOutboundEventData,
  ): Promise<MessageSuccessEventData> {
    const outboundEventDataWithCustomFields =
      this._mergeCustomFieldsAndAccessTokenWithMessageData(messageData, true);

    return sendOutboundMessage(
      outboundEventDataWithCustomFields,
      this._websocketClient,
    );
  }

  /**
   * Load previous messages
   * @returns Promise MoreMessagesLoadedEvent | null
   * @throws LoadMoreMessagesFailedError
   *  * This exception is thrown when the attempt to load more messages fails.
   */
  public async loadMoreMessages(): Promise<MoreMessagesLoadedEvent | null> {
    const { scrollToken, oldestMessageDatetime } =
      SDKVariableStorage.THREAD_DATA ?? {};

    if (isEmpty(scrollToken)) {
      return null;
    }

    const loadMoreMessagesEventData: LoadMoreMessagesEventData = {
      scrollToken: String(scrollToken),
      // @ts-expect-error oldestMessageDatetime is missing in type definition
      oldestMessageDatetime,
      thread: { idOnExternalPlatform: this.idOnExternalPlatform },
    };

    const response = await sendChatEvent(
      createLoadMoreMessagesPayloadData(loadMoreMessagesEventData),
      this._websocketClient,
    );

    if (isLoadMoreMessagesEventData(response)) {
      return response;
    }

    throw new LoadMoreMessagesFailedError('Load more messages failed', {
      response,
    });
  }

  /**
   * Mark all messages in the thread as seen
   */
  public async lastMessageSeen(): Promise<ChatEventData> {
    const payloadData = createLastMessageSeenPayloadData(
      this.idOnExternalPlatform,
    );

    return sendChatEvent(payloadData, this._websocketClient);
  }

  /**
   * Send attachment
   *
   * Raw function to send attachments
   * @param files - An object of this type is returned by the files' property of the HTML <input> element; this lets you access the list of files selected with the <input type="file"> element.
   * @param options - options
   * @throws UploadAttachmentError
   *  * This exception is thrown when the file upload fails. The `error.data` contains information about allowed file size and types.
   */
  public async sendAttachments(
    files: FileList | Array<File> | Array<AttachmentUpload>,
    options: SendMessageOptions = {},
  ): Promise<MessageSuccessEventData> {
    if (isUndefined(files) || files.length === 0) {
      throw new ChatSDKError(
        'FileList must be provided to sendAttachment method',
      );
    }

    const messageData = await createAttachmentUploadMessageData(
      files,
      this.idOnExternalPlatform,
      options,
    );

    return this.sendMessage(messageData);
  }

  /**
   * Send start and stop typing events. It sends stop typing event after the timeout. Repeated calls resets this timeout.
   * @param timeout - The timeout in milliseconds.
   * @param onSendCallback - Callback to be called after the stop typing event is sent.
   */
  public keystroke(timeout = 1000, onSendCallback?: () => void): void {
    if (!this._existsOnPlatform) {
      return;
    }

    if (!this._typingTimeoutID) {
      sendChatEvent(
        createStartTypingEventPayloadData(this.idOnExternalPlatform),
        this._websocketClient,
      );
    }

    clearTimeout(this._typingTimeoutID);
    this._typingTimeoutID = setTimeout(() => {
      this._stopTypingCallback(onSendCallback);
    }, timeout);
  }

  /**
   * Manually send the stop typing event and clear the keystroke timeout.
   */
  public stopTyping(): void {
    this._stopTypingCallback();
  }

  private _stopTypingCallback(onSendCallback?: () => void): void {
    if (!this._typingTimeoutID) {
      return;
    }

    clearTimeout(this._typingTimeoutID);
    this._typingTimeoutID = undefined;
    sendChatEvent(
      createStopTypingEventPayloadData(this.idOnExternalPlatform),
      this._websocketClient,
    );
    if (typeof onSendCallback === 'function') {
      onSendCallback();
    }
  }

  /**
   * Send typing event for message preview after the timeout.
   * @param currentText - current text
   * @param timeout - timeout in milliseconds
   * @returns void
   *
   */
  keystrokeForPreview(currentText: string, timeout = 1250): void {
    this._typingPreviewText = currentText;
    if (this._typingForPreviewTimeoutID) {
      return;
    }

    this._typingForPreviewTimeoutID = setTimeout(() => {
      this.stopTypingForPreview();
    }, timeout);
  }

  /**
   * Manually send the stop typing event for message preview and clear the keystroke timeout.
   * @param shouldSendPreview - should send preview
   * @returns void
   *
   */
  stopTypingForPreview(shouldSendPreview = true): void {
    clearTimeout(this._typingForPreviewTimeoutID);
    this._typingForPreviewTimeoutID = undefined;
    const currentPreviewText = this._typingPreviewText;
    this._typingPreviewText = '';

    if (shouldSendPreview === false) {
      return;
    }
    this.sendMessagePreview(currentPreviewText);
  }

  /**
   * Get Thread Metadata
   * @returns response otherwise throw an error response
   * @throws GetMetadataFailedError
   *  * This exception is thrown when getting thread metadata failed.
   */
  public async getMetadata(): Promise<LoadThreadMetadataChatEvent> {
    const response = await sendChatEvent(
      createLoadThreadMetadataEventPayloadData(this.idOnExternalPlatform),
      this._websocketClient,
    );

    if (isLoadMetadataSuccessPayload(response)) {
      return response;
    }

    throw new GetMetadataFailedError('Get metadata failed', { response });
  }

  public onThreadEvent(
    type: ChatEventType,
    handler: EventListenerFunction,
  ): RemoveListenerFunction {
    const filteredEventHandler = filterCurrentThreadEventHandler(
      this.idOnExternalPlatform,
      handler,
    );

    this._messageEmitter.addEventListener(type, filteredEventHandler);

    return () => {
      this._messageEmitter.removeEventListener(type, filteredEventHandler);
    };
  }

  /**
   * Send current Custom Fields
   * @param selectedFieldIdents - it will send only custom fields with these IDs, if provided. Otherwise, it will send all custom fields.
   */
  public async sendCustomFields(
    selectedFieldIdents?: Array<CustomField['ident']>,
  ): Promise<ChatEventData> {
    const customFieldsArray = getCustomFieldsArray(this._customFields).filter(
      (customField) => {
        if (selectedFieldIdents) {
          return selectedFieldIdents.includes(customField.ident);
        }

        return true;
      },
    );

    const customFieldsData = createSetThreadCustomFieldsPayloadData(
      customFieldsArray,
      this.idOnExternalPlatform,
    );

    return sendChatEvent(customFieldsData, this._websocketClient);
  }

  /**
   * Set thread custom fields and send them
   * @param customFields - custom fields object
   * @example \{ ident: 'value' \}
   */
  public async setCustomFields(
    customFields: CustomFieldsObject,
  ): Promise<void> {
    setCustomFields(this._customFields, customFields);

    if (!this._existsOnPlatform) {
      return;
    }

    await this.sendCustomFields(Object.keys(customFields));
  }

  /**
   * Remove thread custom fields
   * @param ident - custom field ident
   */
  public removeCustomField(ident: CustomField['ident']): void {
    removeCustomField(this._customFields, ident);
  }

  /**
   * Set thread custom field
   * @param ident - custom field name
   * @param value - custom field value
   */
  public setCustomField(
    ident: CustomField['ident'],
    value: CustomField['value'],
  ): Promise<void> {
    return this.setCustomFields({ [ident]: value });
  }

  /**
   * Set thread as archived
   * @returns Promise true
   * @throws ArchiveThreadFailedError
   *  * This exception is thrown when the archive thread failed.
   */
  public async archive(): Promise<true> {
    const response = await sendChatEvent(
      createArchiveThreadEventPayloadData(this.idOnExternalPlatform),
      this._websocketClient,
    );

    if (isThreadArchivedSuccessPayload(response)) {
      return true;
    }

    throw new ArchiveThreadFailedError('Archive Thread failed', { response });
  }

  /**
   * Set thread name
   * @param name - New name of the Thread
   * @returns Promise true
   * @throws SetThreadNameFailedError
   *  * This exception is thrown when the set thread name failed.
   */
  public async setName(name: string): Promise<true> {
    const updateThreadData = createUpdateThreadPayloadData(
      this.idOnExternalPlatform,
      name,
    );

    const response = await sendChatEvent(
      updateThreadData,
      this._websocketClient,
    );

    if (isUpdateThreadSuccess(response)) {
      return true;
    }

    throw new SetThreadNameFailedError('Set Thread name failed', { response });
  }

  /**
   * Send message preview
   * @param text - text
   */
  public async sendMessagePreview(text: string): Promise<void> {
    const payloadData = createSendMessagePreviewPayloadData(
      this.idOnExternalPlatform,
      text,
    );

    if (!this._existsOnPlatform) {
      return;
    }

    await sendChatEvent(payloadData, this._websocketClient);
  }

  /**
   * Send conversation transcript to email
   */
  public async sendTranscript(
    contactNumber: ContactNumber,
    email: string,
  ): Promise<ChatEventData> {
    const payloadData = createSendTranscriptPayloadData(contactNumber, email);

    return sendChatEvent(payloadData, this._websocketClient);
  }

  protected _setExistsOnPlatform(value: boolean): void {
    this._existsOnPlatform = value;
  }

  protected _setExistsOnPlatformBasedOnContactStatus(
    event: ChatCustomEvent,
  ): void {
    const eventDetail = event.detail;

    if (isRecoverSuccessPayload(eventDetail)) {
      this._setExistsOnPlatform(
        eventDetail.data.contact.status !== ContactStatus.CLOSED,
      );
    }
  }

  protected _setCustomerExists(): void {
    this._customer?.setExists(true);
  }

  protected _clearCustomFieldsOnContactStatusChangedToClosed(
    event: ChatCustomEvent,
  ): void {
    const eventDetail = event.detail;

    if (!isContactStatusChangedEvent(eventDetail)) {
      return;
    }

    if (eventDetail.data.case.status === ContactStatus.CLOSED) {
      // Delete custom fields after contact closure to prevent sending custom fields from previous contact.
      this._customFields.clear();
      this._setExistsOnPlatform(false);
    }
  }

  private _mergeCustomFieldsWithMessageData<
    T extends SendMessageEventData | SendOutboundEventData,
  >(messageData: T, isOutbound: boolean): T {
    if (this._existsOnPlatform) {
      // Do not send custom fields with a message to an existing contact.
      // They will be sent in a separate event.
      return {
        ...messageData,
        consumer: { customFields: [] },
        consumerContact: { customFields: [] },
      };
    }

    // Update Thread (Contact) custom fields
    setCustomFieldsFromArray(
      this._customFields,
      messageData.consumerContact.customFields,
    );

    const consumerContact = {
      customFields: getCustomFieldsArray(this._customFields),
    };

    // Update Customer custom fields
    let consumer;
    if (!isOutbound) {
      this._customer?.setCustomFieldsFromArray(
        (messageData as SendMessageEventData).consumer?.customFields ?? [],
      );
      consumer = {
        customFields: this._customer?.getCustomFieldsArray() ?? [],
      };
    }

    return {
      ...messageData,
      consumer,
      consumerContact,
    };
  }

  private _mergeAccessTokenWithMessageData<
    T extends SendMessageEventData | SendOutboundEventData,
  >(messageData: T): T {
    // Access token
    let accessToken;

    const storedThirdPartyToken = getThirdPartyToken();
    const storedAccessToken = getAccessToken();

    if (
      this._isAuthorizationEnabled &&
      (storedThirdPartyToken || storedAccessToken)
    ) {
      accessToken = {
        token: storedThirdPartyToken?.access_token ?? storedAccessToken?.token,
      };
    }

    return {
      ...messageData,
      accessToken,
    };
  }

  private _mergeCustomFieldsAndAccessTokenWithMessageData<
    T extends SendMessageEventData | SendOutboundEventData,
  >(messageData: T, isOutbound: boolean): T {
    const messageDataWithCustomFields = this._mergeCustomFieldsWithMessageData(
      messageData,
      isOutbound,
    );

    return this._mergeAccessTokenWithMessageData(messageDataWithCustomFields);
  }

  private _registerEventHandlers() {
    this.onThreadEvent(ChatEvent.CASE_CREATED, () => {
      this._setCustomerExists();
      this._setExistsOnPlatform(true);
    });
    this.onThreadEvent(ChatEvent.CONTACT_CREATED, () => {
      this._setCustomerExists();
      this._setExistsOnPlatform(true);
    });
    this.onThreadEvent(ChatEvent.THREAD_RECOVERED, (event) => {
      this._setExistsOnPlatformBasedOnContactStatus(event);
      this._setCustomerExists();
    });
    this.onThreadEvent(ChatEvent.CONTACT_STATUS_CHANGED, (event) =>
      this._clearCustomFieldsOnContactStatusChangedToClosed(event),
    );
  }
}
