import { SendOutboundEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/SendOutbound';

import { sendChatEvent } from '../event/sendChatEvent';
import { WebSocketClient } from '../sockets/WebSocketClient';

import {
  isSendMessageSuccessEventData,
  MessageSuccessEventData,
} from './MessageSuccessEventData';
import { SendMessageFailedError } from './SendMessageFailedError';
import { createSendOutboundMessagePayloadData } from './sendOutboundMessagePayload';

export const sendOutboundMessage = async (
  messageData: SendOutboundEventData,
  websocketClient: WebSocketClient | null,
): Promise<MessageSuccessEventData> => {
  const sendOutboundMessagePayloadData =
    createSendOutboundMessagePayloadData(messageData);

  const response = await sendChatEvent(
    sendOutboundMessagePayloadData,
    websocketClient,
  );

  if (isSendMessageSuccessEventData(response)) {
    return response;
  }

  throw new SendMessageFailedError('Send Outbound message failed', response);
};
