import { AwsResponseEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { MoreMessagesLoadedPostbackData } from '@brandembassy/ts-types-chat/dist/awsEvents/MoreMessagesLoaded';

import { ChatEventData } from '../event/ChatEvent';

export interface MoreMessagesLoadedEvent extends ChatEventData {
  data: MoreMessagesLoadedPostbackData;
}

export function isLoadMoreMessagesEventData(
  event: ChatEventData,
): event is MoreMessagesLoadedEvent {
  return (
    (event as MoreMessagesLoadedEvent).type ===
      AwsResponseEventType.MORE_MESSAGES_LOADED &&
    (event as MoreMessagesLoadedEvent)?.data?.messages !== undefined
  );
}
