import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { GenerateAuthorizationTokenData } from '@brandembassy/ts-types-chat/dist/awsEvents/GenerateAuthorizationToken';
import { ThreadIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/thread/ThreadIdOnExternalPlatform';

import { EventPayloadData } from '../sockets/EventPayload';

export function createGenerateAuthorizationTokenPayloadData(
  threadId: ThreadIdOnExternalPlatform,
  url: string,
): EventPayloadData<GenerateAuthorizationTokenData> {
  return {
    eventType: AwsInputEventType.GENERATE_AUTHORIZATION_TOKEN,
    data: {
      thread: {
        idOnExternalPlatform: threadId,
      },
      url,
    },
  };
}
