import { AccessToken } from '@brandembassy/ts-types-chat/dist/authorization/AccessToken';

import { sendChatEvent } from '../event/sendChatEvent';
import { WebSocketClient } from '../sockets/WebSocketClient';

import { AuthorizationError } from './AuthorizationError';
import { createReconnectPayloadData } from './createReconnectPayloadData';
import { CustomerReconnectSuccessPayloadData } from './CustomerReconnectSuccessPayloadData';
import { handleRefreshToken } from './handleRefreshToken';

export async function authorizationReconnect(
  websocketClient: WebSocketClient | null,
  refreshTokenEventCallback: () => Promise<void>,
  accessToken: AccessToken,
  visitorId?: string,
): Promise<CustomerReconnectSuccessPayloadData> {
  const reconnectPayload = createReconnectPayloadData(accessToken, visitorId);

  const response = await sendChatEvent(reconnectPayload, websocketClient);

  const hasError = response.error !== undefined;

  if (hasError) {
    throw new AuthorizationError(
      'Authorization reconnect failed',
      response.error,
    );
  }

  const successReconnectResponse: CustomerReconnectSuccessPayloadData = {
    reconnected: true,
  };

  handleRefreshToken(accessToken, refreshTokenEventCallback);

  return successReconnectResponse;
}
