import { BrandId } from '@brandembassy/ts-types-platform/dist/brand/BrandId';
import { ChannelId } from '@brandembassy/ts-types-platform/dist/channel/ChannelId';

import { NetworkRequestMetadata } from '../request/NetworkRequestMetadata';
import { SecureSessionsType } from '../securedSession/SecureSessionsType';
import { ThirdPartyToken } from '../securedSession/ThirdPartyToken';
import { ICacheStorage } from '../storage/CacheStorage';
import { IStorage } from '../storage/Storage';

export interface ThreadData {
  contactId?: string;
  oldestMessageDatetime?: string;
  scrollToken?: string;
}

export interface ChatSDKVariableStorage {
  ACCESS_TOKEN: string;
  ACCESS_TOKEN_EXPIRES_IN: number;
  APP_NAME: string;
  APP_VERSION: string;
  AUTHORIZATION_CODE?: string;
  BRAND_ID: BrandId;
  CACHE_STORAGE?: ICacheStorage;
  CHANNEL_ID: ChannelId;
  DESTINATION?: string;
  ENDPOINT_AUTHORIZE: string;
  ENDPOINT_CHAT: string;
  ENDPOINT_GATEWAY: string;
  IDENTITY_TOKEN?: string;
  LANGUAGE: string;
  REQUEST_METADATA: NetworkRequestMetadata;
  SECURED_SESSION: SecureSessionsType;
  STORAGE?: IStorage;
  THIRD_PARTY_TOKEN?: ThirdPartyToken;
  THREAD_DATA: ThreadData;
  VISITOR_ID?: string;
  VISIT_ID?: string;
}

export const SDKVariableStorage: Partial<ChatSDKVariableStorage> = {};

export const clearVariableStorage = (): void => {
  Object.keys(SDKVariableStorage).forEach((key) => {
    delete SDKVariableStorage[key as keyof ChatSDKVariableStorage];
  });
};
