export class ChatSdk {
  onAuthorization?: AuthorizationCallback;
  onError?: ErrorCallback;
  onRawEvent?: RawEventCallback;

  public isLivechat: boolean | undefined;
  public channelId: ChannelId = '';

  #customer: Customer;
  readonly #customerImage: string | undefined;
  readonly #customerName: string | undefined;
  readonly #customerId: CustomerIdentityIdOnExternalPlatform | undefined;
  readonly #isThirdPartyCookiesSupported: boolean | undefined;
  #isAuthorizationEnabled: boolean | undefined;
  #websocketClient: WebSocketClient | null = null;

  readonly #contactCustomFieldsQueue: CustomFieldsMap = new Map();
  readonly #threadCache: Map<
    ThreadIdOnExternalPlatform,
    Thread | LivechatThread
  > = new Map();
  readonly #incomingChatEventMiddleware = new ChatEventMiddleware();
  readonly #messageEmitter: IChatEventTarget;
  readonly #ready = getPromiseWithResolvers<void>();

  constructor(options: ChatSDKOptions) {
    if (options === undefined) {
      throw new ChatSDKError(
        'No options was provided for initialization of ChatSdk',
      );
    }

    this.#customerImage = options.customerImage;
    this.#customerName = options.customerName;
    this.#customerId = options.customerId;

    initializeSdkVariables(options);

    this.#customer = CustomerInstance.getInstance();

    if (typeof options.customerId !== 'undefined') {
      setCustomerIdFromOptions(options.customerId);
    }

    const { brandId, channelId } = getBrandIdAndChannelId();

    this.onAuthorization = options.onAuthorization;
    this.onError = options.onError;
    this.onRawEvent = options.onRawEvent;

    // Register middleware for incoming chat events
    this.#incomingChatEventMiddleware.register(chatEventMapper);
    this.#incomingChatEventMiddleware.register(sdkVersionCheck);
    this.#incomingChatEventMiddleware.register(storeThreadScrollDataMiddleware);
    this.#incomingChatEventMiddleware.register(storeContactIdMiddleware);

    this.#messageEmitter = new ChatEventTarget();

    try {
      if (isNaN(brandId)) {
        throw new Error('Missing BrandID');
      }
      if (channelId === undefined) {
        throw new Error('Missing ChannelId');
      }
      if (options.customerId === undefined && !isSecureSessions()) {
        throw new Error('Missing CustomerId');
      }

      this.channelId = channelId;
      this.isLivechat = options.isLivechat;
      this.#isAuthorizationEnabled =
        getSecuredSessionType() === SecureSessions.THIRD_PARTY ||
        options.isAuthorizationEnabled;
      this.#isThirdPartyCookiesSupported = options.isThirdPartyCookiesSupported;

      this.#initEnvironment(options);
    } catch (error: unknown) {
      this.#onErrorHandler(error);
    }
  }

  /**
   * Initiate a WebSocket connection
   * @param authorizationCode - authorization code
   * @returns Promise<boolean> - true if the connection was created, false if the connection already exists
   * @throws ChatSDKError
   */
  public async connect(authorizationCode?: string): Promise<boolean> {
    if (authorizationCode) {
      SDKVariableStorage.AUTHORIZATION_CODE = authorizationCode;
    }
    // Do not create a new connection if there is already one
    if (this.#websocketClient !== null) {
      return false;
    }
    await this.#createConnection();

    if (isSecureSessions()) {
      await this.ready();
    }

    // Init Customer instance after the websocketClient is initialized
    if (this.#customerId !== undefined) {
      this.#customer.setId(this.#customerId);
    }

    if (this.#customerName !== undefined) {
      this.#customer.setName(this.#customerName);
    }

    if (this.#customerImage !== undefined) {
      this.#customer.setImage(this.#customerImage);
    }

    if (this.#websocketClient !== null) {
      this.#customer.setWebsocketClient(this.#websocketClient);
    }

    // Mark the instance as ready (if not already)
    this.#ready.resolve();

    return true;
  }

  public async ready(): Promise<void> {
    return this.#ready.promise;
  }

  #onErrorHandler(error: unknown): void {
    const sdkError =
      error instanceof ChatSDKError ? error : new ChatSDKError(error);

    if (typeof this.onError === 'function') {
      this.onError(sdkError);

      return;
    }

    throw sdkError;
  }

  /**
   * Get channel info
   * Returns channel info like feature toggle status, translations, file upload restrictions, theme color settings etc.
   * @returns ChannelInfo
   * @throws ChatSDKError
   */
  public async getChannelInfo(): Promise<ChannelInfo> {
    return fetchChannelInfo(
      SDKVariableStorage.BRAND_ID as BrandId,
      SDKVariableStorage.CHANNEL_ID as ChannelId,
      SDKVariableStorage.LANGUAGE ?? getBrowserLanguage(),
      SDKVariableStorage.ENDPOINT_CHAT as string,
      SDKVariableStorage.REQUEST_METADATA?.httpHeaders || {},
    );
  }

  /**
   * Get channel availability
   * Returns channel availability Online/Offline
   * @returns ChannelAvailabilityResponse
   * @throws ChatSDKError
   */
  public async getChannelAvailability(): Promise<ChannelAvailabilityResponse> {
    return fetchChannelAvailability(
      SDKVariableStorage.BRAND_ID as BrandId,
      SDKVariableStorage.CHANNEL_ID as ChannelId,
      SDKVariableStorage.ENDPOINT_CHAT as string,
      SDKVariableStorage.REQUEST_METADATA?.httpHeaders || {},
      SDKVariableStorage.APP_NAME,
    );
  }

  /**
   * Send Authorization Event
   * @deprecated - use Secured Session flow instead (SDK option `securedSession` and {@link ChatSdk.connect})
   * @param authorizationCode - authorization code
   * @param visitorId - visitor id
   * @param browserFingerprint - BrowserFingerprint object, use getBrowserFingerprint helper function to create it
   * @throws AuthorizationError
   *  * This exception is thrown when the authorization or refresh token fails
   */
  public async authorize(
    authorizationCode?: string,
    visitorId?: VisitorId,
    browserFingerprint?: BrowserFingerprint,
  ): Promise<
    | ConsumerAuthorizationSuccessPayloadData
    | CustomerReconnectSuccessPayloadData
  > {
    await this.ready(); // Wait for the instance to be ready

    const accessToken = getAccessToken();

    // Reconnect first
    if (accessToken !== null) {
      try {
        const reconnectResponse = await authorizationReconnect(
          this.#websocketClient,
          this.#sendRefreshTokenEvent,
          accessToken,
          visitorId,
        );

        return reconnectResponse;
      } catch (error) {
        // invalid token, continue with authorization
      }
    }

    const basicChannelInfoRequest = handleBasicChannelInfo(
      this.#isAuthorizationEnabled,
      this.isLivechat,
    );

    // Authorization
    const authorizationPayloadData = createAuthorizationPayloadData(
      authorizationCode ?? SDKVariableStorage.AUTHORIZATION_CODE ?? null,
      visitorId,
      browserFingerprint,
    );

    const authEvent = createEvent(
      createEventPayload(authorizationPayloadData),
      generateId(),
      AwsChatWindowEventActionRoute.REGISTER,
    );

    const authorizationRequest = makeRequest(authEvent, this.#websocketClient);

    const [authResponse, basicChannelInfo] = await Promise.all([
      authorizationRequest,
      basicChannelInfoRequest,
    ]);

    if (!isAuthSuccessEvent(authResponse)) {
      this.#websocketClient?.disconnect();

      throw new AuthorizationError('Authorization failed', authResponse.error);
    }

    // Auth successful - Use the response data
    const { consumerIdentity, customer, contact } = authResponse.data;

    // Setup Customer Identity and Customer Custom Fields
    const customerId = consumerIdentity?.idOnExternalPlatform;
    if (!isCustomerIdentityIdValid(customerId)) {
      this.#websocketClient?.disconnect();

      throw new ChatSDKError('Invalid customer identity');
    }

    setCustomerIdentity(this.#customer, consumerIdentity);

    if (customer?.customFields) {
      this.#customer.setCustomFieldsFromArray(customer.customFields);
    }

    // Queue Contact Custom Fields from the response
    if (contact?.customFields) {
      setCustomFieldsFromArray(
        this.#contactCustomFieldsQueue,
        contact.customFields,
      );
    }

    // Set up the thread settings
    this.#isAuthorizationEnabled = basicChannelInfo.isAuthorizationEnabled;
    this.isLivechat = basicChannelInfo.isLivechat;

    // Set up the access token and its refresh
    if (authResponse.data.accessToken?.token !== undefined) {
      setAccessToken(authResponse.data.accessToken);
      handleRefreshToken(
        authResponse.data.accessToken,
        this.#sendRefreshTokenEvent,
      );
    }

    return authResponse.data;
  }

  /**
   * Generate Authorization Token from the given url
   *
   * @deprecated the additional message content is handled internally, there is no need to use this method
   * @param threadIdOnExternalPlatform - Thread Id
   * @param url - Authorization Service URL
   */
  public async generateAuthorizationToken(
    threadIdOnExternalPlatform: ThreadIdOnExternalPlatform,
    url: string,
  ): Promise<AuthorizationToken> {
    if (this.#websocketClient === null) {
      throw new ChatSDKError('Websocket client is not initialized');
    }

    return getAuthorizationTokenForUrl(
      threadIdOnExternalPlatform,
      url,
      this.#websocketClient,
    );
  }

  /**
   * Register handler to chat event
   *
   * @param type - type of chat event
   * @param handler - event handler
   * @returns function to unregister handler
   */
  public onChatEvent(
    type: ChatEventType,
    handler: (event: ChatCustomEvent) => void,
  ): RemoveListenerFunction {
    this.#messageEmitter.addEventListener(type, handler);

    return () => {
      this.#messageEmitter.removeEventListener(type, handler);
    };
  }

  /**
   * Get Customer instance
   */
  public getCustomer(): Customer {
    return this.#customer;
  }

  /**
   * Get Thread instance by id
   * @param id - thread id
   * @returns instance of thread based on channel settings
   */
  public getThread(id: ThreadIdOnExternalPlatform): Thread | LivechatThread {
    if (id === '') {
      throw new ChatSDKError('Cannot get thread because id is empty');
    }

    if (isNull(this.#websocketClient)) {
      throw new ChatSDKError(
        'Cannot get thread because websocket is disconnected',
      );
    }

    if (isNil(id)) {
      throw new ChatSDKError('Cannot get thread because id is undefined');
    }

    const cachedThread = this.#threadCache.get(id);
    if (!isUndefined(cachedThread)) {
      return cachedThread;
    }

    if (this.isLivechat === true) {
      const liveThread = new LivechatThread(
        id,
        this.#websocketClient,
        this.#messageEmitter,
        this.#customer,
        this._getContactCustomFieldsFromQueue(),
        this.#isAuthorizationEnabled,
      );
      this.#threadCache.set(id, liveThread);

      return liveThread;
    }

    const thread = new Thread(
      id,
      this.#websocketClient,
      this.#messageEmitter,
      this.#customer,
      this._getContactCustomFieldsFromQueue(),
      this.#isAuthorizationEnabled,
    );
    this.#threadCache.set(id, thread);

    return thread;
  }

  /**
   * Get list of available threads
   * @returns list of threads
   */
  public async getThreadList(): Promise<Array<ThreadView> | null> {
    if (isNull(this.#websocketClient)) {
      throw new ChatSDKError(
        'Cannot get thread list because websocket is disconnected',
      );
    }

    const eventPayload = {
      eventType: AwsInputEventType.FETCH_THREAD_LIST,
      data: {},
    };

    const result = await sendChatEvent(eventPayload, this.#websocketClient);

    if (!isThreadListFetchedPostbackData(result.data)) {
      throw new ChatSDKError(
        'Invalid response from fetch thread list (getThreadList)',
      );
    }

    return result.data.threads;
  }

  /**
   * Get access to a websocket connection
   * @returns WebSocketClient instance
   */
  public getWebsocketClient(): WebSocketClient | null {
    return this.#websocketClient;
  }

  /**
   * Send the Offline Message
   * @param offlineMessageData - offline message data (name, email, message)
   * @returns success
   * @throws SendMessageFailedError
   *  * This exception is thrown when a message fails to send. The error contains (`error.data`) a response from the backend with details.
   */
  public async sendOfflineMessage(
    offlineMessageData: OfflineMessageData,
  ): Promise<MessageSuccessEventData> {
    return sendOfflineMessage(offlineMessageData, this.#websocketClient);
  }

  /**
   * Recover thread data
   * @param threadIdOnExternalPlatform - thread id on external platform
   * @returns thread session data
   * @throws ThreadRecoverFailedError
   *  * This exception is thrown when the recover fails or the thread does not exist.
   */
  public recoverThreadData(
    threadIdOnExternalPlatform:
      | ThreadIdOnExternalPlatform
      | undefined = undefined,
  ): AbortablePromise<ThreadRecoveredChatEvent> {
    return new AbortablePromise(async (resolve, reject) => {
      const recoverEvent = createRecoverThreadPayloadData(
        threadIdOnExternalPlatform,
      );
      const recoverResponse = await sendChatEvent(
        recoverEvent,
        this.#websocketClient,
      );

      if (isRecoverSuccessPayload(recoverResponse)) {
        // Create a Thread instance after success recover
        this.getThread(recoverResponse.data.thread.idOnExternalPlatform);
        // Emit the 'recover' event to properly set up the Thread instance.
        this.#messageEmitter.dispatchEvent(
          new ChatCustomEvent(ChatEvent.THREAD_RECOVERED, {
            detail: recoverResponse,
          }),
        );

        resolve(recoverResponse);
      } else {
        reject(
          new ThreadRecoverFailedError('Thread not found', {
            response: recoverResponse,
          }),
        );
      }
    });
  }

  /**
   * Recover livechat thread data
   * @param threadIdOnExternalPlatform - thread id on external platform
   * @returns thread livechat session data
   * @throws ThreadRecoverFailedError
   *  * This exception is thrown when the recover fails or the thread does not exist.
   */
  public recoverLivechatThreadData(
    threadIdOnExternalPlatform:
      | ThreadIdOnExternalPlatform
      | undefined = undefined,
  ): AbortablePromise<ThreadRecoveredChatEvent> {
    return new AbortablePromise(async (resolve, reject) => {
      const recoverEvent = createRecoverLivechatThreadPayloadData(
        threadIdOnExternalPlatform,
      );
      const recoverResponse = await sendChatEvent(
        recoverEvent,
        this.#websocketClient,
      );

      if (isRecoverSuccessPayload(recoverResponse)) {
        // Create a Thread instance after success recover
        this.getThread(recoverResponse.data.thread.idOnExternalPlatform);
        // Emit the 'recover' event to properly set up the Thread instance.
        this.#messageEmitter.dispatchEvent(
          new ChatCustomEvent(ChatEvent.LIVECHAT_RECOVERED, {
            detail: recoverResponse,
          }),
        );

        resolve(recoverResponse);
      } else {
        reject(
          new ThreadRecoverFailedError('Thread not found', {
            response: recoverResponse,
          }),
        );
      }
    });
  }

  /**
   * Reset the ChatSdk session and clear it from customer data
   * - it disconnects the WS connection and creates a new one
   * - generates new IDs if not provided
   */
  public async resetSession(
    customerId: CustomerIdentityIdOnExternalPlatform = generateId(),
    customerName: string = '',
    customerImage: string = '',
    visitorId: VisitorId = '',
    visitId: VisitId = generateId(),
  ): Promise<void> {
    // Close the connection and clear the session data (customer, tokens, etc.)
    this.#websocketClient?.disconnect();
    this.#websocketClient = null;
    this.#threadCache.clear();
    this.#contactCustomFieldsQueue.clear();
    resetAccessToken();
    resetThirdPartyToken();
    clearCachedTransactionToken();

    this.#customer.destroy();

    // Reinitialize
    SDKVariableStorage.VISIT_ID = visitId;
    SDKVariableStorage.VISITOR_ID = visitorId;

    try {
      await this.#createConnection();
      this.#customer = new Customer(
        customerId,
        customerName,
        customerImage,
        this.#websocketClient,
      );
    } catch (e) {
      this.#onErrorHandler(e);
    }
  }

  async #createSocketUrl(): Promise<string> {
    const { brandId, channelId } = getBrandIdAndChannelId();
    const endpoint = SDKVariableStorage.ENDPOINT_GATEWAY;
    const appVersion = SDKVariableStorage.APP_VERSION;
    let accessToken: string | undefined;

    assertNonNullable(endpoint);

    const visitorId = SDKVariableStorage.VISITOR_ID ?? '';

    if (isSecureSessions()) {
      const customerId = shouldHandleCustomerIdForSecureSessions()
        ? this.#customer.getId()
        : null;
      const authorizationCode = this.#isAuthorizationEnabled
        ? SDKVariableStorage.AUTHORIZATION_CODE
        : undefined;
      const useCache = getSecuredSessionType() !== SecureSessions.THIRD_PARTY;
      const cookieStorageFallback =
        getSecuredSessionType() === SecureSessions.SECURED_COOKIES &&
        !this.#isThirdPartyCookiesSupported;

      try {
        const transactionTokenResponse = await getTransactionToken(
          authorizationCode,
          customerId,
          cookieStorageFallback,
          useCache,
        );
        accessToken = transactionTokenResponse.accessToken;

        const { customerIdentity, thirdParty, contact, identityToken } =
          transactionTokenResponse;
        // Set Customer Identity
        if (customerIdentity) {
          setCustomerIdentity(this.#customer, customerIdentity);
        }
        // Set Third Party Token
        if (thirdParty) {
          handleNewThirdPartyToken(thirdParty);
        }
        // Set Contact custom fields
        if (contact?.customFields) {
          setCustomFieldsFromArray(
            this.#contactCustomFieldsQueue,
            contact.customFields,
          );
        }
        // Set and store fallback identityToken
        if (identityToken) {
          storeIdentityToken(identityToken);
        }
        // Propagate authorization success with context
        if (this.onAuthorization) {
          this.onAuthorization('success', transactionTokenResponse);
        }
      } catch (error) {
        if (isIpAddressBlockedError(error)) {
          if (typeof this.onError === 'function') {
            this.onError(new IpAddressBlockedError(ERROR_IP_ADDRESS_BLOCKED));
          }

          throw new IpAddressBlockedError(ERROR_IP_ADDRESS_BLOCKED);
        } else {
          const sdkError = new ChatSDKError(
            ERROR_CANNOT_FETCH_TRANSACTION_TOKEN,
            { error },
          );
          // Propagate authorization error with context
          if (this.onAuthorization) {
            this.onAuthorization('error', {});
          }
          this.#onErrorHandler(sdkError);

          throw sdkError;
        }
      }
    }

    // Mark the instance as ready
    this.#ready.resolve();

    const customMetadataHeaders =
      SDKVariableStorage.REQUEST_METADATA?.websocketQuery ?? {};

    const queryParameters = {
      ...customMetadataHeaders,
      v: appVersion,
      sdkPlatform: 'web',
      sdkVersion: CHAT_SDK_VERSION,
      brandId,
      channelId,
      ...(isSecureSessions() ? {} : { customerId: this.#customer.getId() }),
      visitorId,
      ...(isSecureSessions() ? { transactionToken: accessToken } : {}),
    };

    const queryParametersAsString =
      createQueryParametersAsString(queryParameters);

    try {
      const url = new URL(endpoint);
      url.search = queryParametersAsString;

      return url.toString();
    } catch (error) {
      this.#onErrorHandler(
        new ChatSDKError(
          `Invalid URL: ${endpoint}. Make sure it is a valid URL.`,
          { error },
        ),
      );

      return '';
    }
  }

  async #createConnection(): Promise<void> {
    try {
      this.#websocketClient = this.#initWS();
      await this.#websocketClient.connect();
    } catch (error) {
      this.#onErrorHandler(error);
    }
  }

  _getContactCustomFieldsFromQueue(): CustomFieldsObject {
    if (this.#contactCustomFieldsQueue.size > 0) {
      const fields = getCustomFields(this.#contactCustomFieldsQueue);
      this.#contactCustomFieldsQueue.clear();

      return fields;
    }

    return {};
  }

  /**
   * @deprecated - do not use
   */
  #sendRefreshTokenEvent = async (): Promise<void> => {
    const accessToken = getAccessToken();
    if (isNull(accessToken)) {
      return;
    }

    const refreshTokenResponse = await sendChatEvent(
      createRefreshTokenPayload(accessToken.token),
      this.#websocketClient,
    );

    if (isTokenRefreshedSuccessResponse(refreshTokenResponse)) {
      setAccessToken(refreshTokenResponse.data.accessToken);

      handleRefreshToken(
        refreshTokenResponse.data.accessToken,
        this.#sendRefreshTokenEvent,
      );

      return;
    }

    throw new AuthorizationError(
      'An error occurred while refreshing the access token',
      refreshTokenResponse.error,
    );
  };

  /**
   * Setup Environment endpoints
   */
  #initEnvironment(options: ChatSDKOptions): void {
    // Set custom environment
    if (options.environment === EnvironmentName.custom) {
      if (
        isEmpty(options.customEnvironment) ||
        isNil(options.customEnvironment)
      ) {
        throw new ChatSDKError(
          'customEnvironment must be provided when environment is set to "custom"',
        );
      }

      setEnvironmentEndpoints(options.customEnvironment);

      return;
    }

    // Set predefined environment
    const endpoints = buildEnvironmentEndpoints(options.environment);
    setEnvironmentEndpoints(endpoints);
  }

  #initWS(): WebSocketClient {
    const client = new WebSocketClient(
      this.onError,
      this.#createSocketUrl.bind(this),
    );

    client.on(WebSocketClientEvent.MESSAGE, async (event: ChatCustomEvent) => {
      try {
        if (typeof this.onRawEvent === 'function') {
          this.onRawEvent(event);
        }

        const eventData = await getEventData(
          parseWsMessageEvent(event),
          this.#websocketClient,
        );

        const parsedEvent =
          this.#incomingChatEventMiddleware.process(eventData);

        if (!isNil(parsedEvent)) {
          const { type } = parsedEvent;

          onMessageHandler(parsedEvent);

          this.#messageEmitter.dispatchEvent(
            new ChatCustomEvent(type ?? '', {
              detail: parsedEvent,
            }),
          );
        }
      } catch (error: unknown) {
        this.#onErrorHandler(error);
      }
    });

    return client;
  }
}
