import { LoadMoreMessagesEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/LoadMoreMessages';
import { SendMessageEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/SendMessage';
import { ThreadIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/thread/ThreadIdOnExternalPlatform';

import { Customer } from '../../customer/Customer';
import { CustomFieldsObject } from '../../customField/CustomFields';
import { ChatSDKError } from '../../error/ChatSDKError';
import { ChatCustomEvent, ChatEvent } from '../../event/ChatEvent';
import { IChatEventTarget } from '../../event/ChatEventTarget';
import { sendChatEvent } from '../../event/sendChatEvent';
import {
  isLoadMoreMessagesEventData,
  MoreMessagesLoadedEvent,
} from '../../message/loadMoreMessagesEventData';
import { createLoadMoreMessagesPayloadData } from '../../message/loadMoreMessagesPayload';
import { MessageSuccessEventData } from '../../message/MessageSuccessEventData';
import { WebSocketClient } from '../../sockets/WebSocketClient';
import { AbortablePromise } from '../../utils/AbortablePromise';
import { isEmpty, isUndefined } from '../../utils/is';
import { SDKVariableStorage } from '../../variables/VariableStorage';
import { isRecoverSuccessPayload } from '../isRecoverSuccessPayload';
import { LoadMoreMessagesFailedError } from '../LoadMoreMessagesFailedError';
import { Thread } from '../Thread';
import { ThreadRecoveredData } from '../ThreadRecoveredData';
import { ThreadRecoverFailedError } from '../ThreadRecoverFailedError';

import { createEndThreadPayloadData } from './createEndThreadPayloadData';
import { createRecoverLivechatThreadPayloadData } from './recoverLivechatThreadPayload';

export class LivechatThread extends Thread {
  protected _isInitialized: boolean = false;
  protected _canSendMessage: boolean = true;

  constructor(
    idOnExternalPlatform: ThreadIdOnExternalPlatform,
    websocketClient: WebSocketClient,
    messageEmitter: IChatEventTarget,
    customer: Customer | null,
    customFields: CustomFieldsObject = {},
    isAuthorizationEnabled: boolean = false,
  ) {
    super(
      idOnExternalPlatform,
      websocketClient,
      messageEmitter,
      customer,
      customFields,
      isAuthorizationEnabled,
    );

    this._registerLivechatEventHandlers();
  }

  /**
   * Recover existing live chat
   * @returns AbortablePromise ThreadRecoveredData
   * @throws ThreadRecoverFailedError
   *  * This exception is thrown when the recover fails or the thread does not exist.
   */
  public override recover(): AbortablePromise<ThreadRecoveredData> {
    return new AbortablePromise(async (resolve, reject) => {
      const response = await sendChatEvent(
        createRecoverLivechatThreadPayloadData(this.idOnExternalPlatform),
        this._websocketClient,
      );

      if (isRecoverSuccessPayload(response)) {
        resolve(response.data);
      } else {
        reject(new ThreadRecoverFailedError('Thread not found', { response }));
      }
    });
  }

  public override async sendMessage(
    messageData: SendMessageEventData,
  ): Promise<MessageSuccessEventData> {
    if (this._canSendMessage === false) {
      throw new ChatSDKError('Cannot send more messages to Contact');
    }

    return super.sendMessage(messageData);
  }

  /**
   * Start livechat
   * @param initialMessageText - initial message text
   */
  public async startChat(
    initialMessageText = 'Begin conversation',
  ): Promise<MessageSuccessEventData | void> {
    if (this._isInitialized) {
      throw new ChatSDKError('Chat is already initialized');
    }

    try {
      const result = await this.sendTextMessage(initialMessageText);
      this._isInitialized = true;

      return result;
    } catch (error: unknown) {
      if (error instanceof Error) {
        throw new ChatSDKError(
          `Sending initial message failed because of (${error.message})`,
        );
      }

      return;
    }
  }

  public async endChat(): Promise<void> {
    const threadData = SDKVariableStorage.THREAD_DATA ?? {};
    const contactId = threadData?.contactId;

    if (isUndefined(contactId)) {
      throw new ChatSDKError(
        'Cannot end Chat because of missing ContactId in the storage',
      );
    }

    await sendChatEvent(
      createEndThreadPayloadData(this.idOnExternalPlatform, contactId),
      this._websocketClient,
    );

    return;
  }

  /**
   * Load previous messages
   * @returns Promise MoreMessagesLoadedEvent | null
   * @throws LoadMoreMessagesFailedError
   *  * This exception is thrown when the attempt to load more messages fails.
   */
  public async loadMoreMessages(): Promise<MoreMessagesLoadedEvent | null> {
    const { scrollToken, oldestMessageDatetime, contactId } =
      SDKVariableStorage.THREAD_DATA ?? {};

    if (isEmpty(scrollToken) || isEmpty(contactId)) {
      return null;
    }

    const loadMoreMessagesEventData: LoadMoreMessagesEventData = {
      scrollToken: String(scrollToken),
      // @ts-expect-error oldestMessageDatetime is missing in type definition
      oldestMessageDatetime,
      thread: { idOnExternalPlatform: this.idOnExternalPlatform },
      // If a contactId is provided, the search will return messages only for this contact
      contact: {
        id: contactId,
      },
    };

    const response = await sendChatEvent(
      createLoadMoreMessagesPayloadData(loadMoreMessagesEventData),
      this._websocketClient,
    );

    if (isLoadMoreMessagesEventData(response)) {
      return response;
    }

    throw new LoadMoreMessagesFailedError('Load more messages failed', {
      response,
    });
  }

  private _registerLivechatEventHandlers() {
    this.onThreadEvent(
      ChatEvent.LIVECHAT_RECOVERED,
      (event: ChatCustomEvent) => {
        if (!isRecoverSuccessPayload(event.detail)) {
          return;
        }

        this._setCustomerExists();
        this._setExistsOnPlatform(true);
      },
    );
  }
}
