import { AwsResponseEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { PushUpdateContext } from '@brandembassy/ts-types-platform/dist/pushUpdate/PushUpdateContext';
import { PushUpdateEventType } from '@brandembassy/ts-types-platform/dist/pushUpdate/PushUpdateEventType';

import { MessageFailedEventData } from '../message/MessageSuccessEventData';

export const ChatEvent = {
  ...PushUpdateEventType,
  ...AwsResponseEventType,
  AGENT_TYPING_STARTED: 'AgentTypingStarted',
  AGENT_TYPING_ENDED: 'AgentTypingEnded',
  ASSIGNED_AGENT_CHANGED: 'AssignedAgentChanged',
  CONTACT_CREATED: 'ContactCreated',
  CONTACT_STATUS_CHANGED: 'ContactStatusChanged',
  CONTACT_TO_ROUTING_QUEUE_ASSIGNMENT_CHANGED:
    'ContactToRoutingQueueAssignmentChanged',
} as const;

export class ChatCustomEvent<
  T extends ChatEventData = ChatEventData,
> extends CustomEvent<T> {}

export interface ChatEventData {
  context?: [] | PushUpdateContext;
  createdAt: string;
  createdAtWithMilliseconds?: string;
  data: unknown;
  error?: MessageFailedEventData['error'];
  id: string;
  type?: ChatEventType;
}

export type ChatEventKey = keyof typeof ChatEvent;
export type ChatEventType = typeof ChatEvent[ChatEventKey];
