import { Message } from '@brandembassy/ts-types-platform/dist/message/Message';

import { isUndefined } from '../utils/is';

export function isMessage(item: unknown): item is Message {
  const hasId = isUndefined((item as Message).id) === false;
  const hasDirection = isUndefined((item as Message).direction) === false;
  const hasMessageContent =
    isUndefined((item as Message).messageContent) === false;

  return hasId && hasDirection && hasMessageContent;
}
