import { ChatSDKError } from '../error/ChatSDKError';
import {
  isSecureSessions,
  shouldHandleCustomerIdForSecureSessions,
} from '../securedSession/getSecuredSessionType';
import { isEmpty } from '../utils/is';

import { CustomerInstance } from './CustomerInstance';

export function setCustomerIdFromOptions(customerIdOption: unknown): void {
  if (
    isSecureSessions() &&
    shouldHandleCustomerIdForSecureSessions() === false &&
    typeof customerIdOption !== 'undefined'
  ) {
    throw new ChatSDKError(
      'The CustomerId cannot be set when secured sessions are enabled',
    );
  }

  if (
    isSecureSessions() &&
    shouldHandleCustomerIdForSecureSessions() &&
    isEmpty(customerIdOption)
  ) {
    throw new ChatSDKError(
      'The CustomerId cannot be empty string when "anonymous" secured sessions are enabled',
    );
  }

  if (typeof customerIdOption === 'string') {
    CustomerInstance.getInstance().setId(customerIdOption);
  }
}
