import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { SendOutboundEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/SendOutbound';

import { EventPayloadData } from '../sockets/EventPayload';

export const createSendOutboundMessagePayloadData = (
  eventData: SendOutboundEventData,
): EventPayloadData<SendOutboundEventData> => ({
  eventType: AwsInputEventType.SEND_OUTBOUND,
  data: eventData,
});
