import { ThreadIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/thread/ThreadIdOnExternalPlatform';

import { ChatCustomEvent } from '../event/ChatEvent';
import { EventListenerFunction } from '../event/ListenerFunction';

interface DataWithThread {
  case?: {
    threadIdOnExternalPlatform?: ThreadIdOnExternalPlatform;
  };
  message?: {
    threadIdOnExternalPlatform?: ThreadIdOnExternalPlatform;
  };
  messagePreview?: {
    threadIdOnExternalPlatform?: ThreadIdOnExternalPlatform;
  };
  thread?: {
    idOnExternalPlatform?: ThreadIdOnExternalPlatform;
  };
}

const getThreadId = (data: unknown): ThreadIdOnExternalPlatform | undefined => {
  const dataWithThread = data as DataWithThread;

  return (
    dataWithThread?.thread?.idOnExternalPlatform ??
    dataWithThread?.case?.threadIdOnExternalPlatform ??
    dataWithThread?.message?.threadIdOnExternalPlatform ??
    dataWithThread?.messagePreview?.threadIdOnExternalPlatform
  );
};

export const filterCurrentThreadEventHandler =
  (threadId: ThreadIdOnExternalPlatform, handler: EventListenerFunction) =>
  (event: ChatCustomEvent): void => {
    const eventData = event.detail.data;

    const eventThreadId = getThreadId(eventData);

    if (eventThreadId === threadId) {
      handler(event);
    }
  };
