import { S3EventData } from '@brandembassy/ts-types-platform/dist/largeEvent/S3Event';
import { PushUpdateEventType } from '@brandembassy/ts-types-platform/dist/pushUpdate/PushUpdateEventType';

import { ChatEvent, ChatEventData } from '../event/ChatEvent';

export interface S3ChatEvent extends ChatEventData {
  data: S3EventData;
  type: typeof ChatEvent.EVENT_IN_S3;
}

export function isS3ChatEvent(event: unknown): event is S3ChatEvent {
  return (event as S3ChatEvent).type === PushUpdateEventType.EVENT_IN_S3;
}
