import { Message } from '@brandembassy/ts-types-platform/dist/message/Message';
import { MessageDirection } from '@brandembassy/ts-types-platform/dist/message/MessageDirection';

/**
 * Get message author name of given message
 * @param message - message
 * @returns message author name
 */
export const getAuthor = (message: Message): string =>
  message.direction === MessageDirection.INBOUND
    ? message.authorEndUserIdentity?.fullName ?? ''
    : `${message.authorUser?.firstName ?? ''} ${
        message.authorUser?.surname ?? ''
      }`.trim();
