import { ChatSDKError } from '../error/ChatSDKError';

export enum EnvironmentName {
  AE1 = 'AE1',
  AU1 = 'AU1',
  AU2 = 'AU2',
  CA1 = 'CA1',
  EU1 = 'EU1',
  EU2 = 'EU2',
  JO1 = 'JO1',
  JP1 = 'JP1',
  KR1 = 'KR1',
  NA1 = 'NA1',
  NA2 = 'NA2',
  UK1 = 'UK1',
  UK2 = 'UK2',
  ZA1 = 'ZA1',
  custom = 'custom',
}

const EnvironmentToPublicDNS: Record<EnvironmentName, string> = {
  [EnvironmentName.AE1]: 'nicecxone.com',
  [EnvironmentName.AU1]: 'niceincontact.com',
  [EnvironmentName.AU2]: 'nicecxone-sov1.au',
  [EnvironmentName.CA1]: 'niceincontact.com',
  [EnvironmentName.EU1]: 'niceincontact.com',
  [EnvironmentName.EU2]: 'nicecxone-sov1.eu',
  [EnvironmentName.JO1]: 'nicecxone.com',
  [EnvironmentName.JP1]: 'niceincontact.com',
  [EnvironmentName.KR1]: 'nicecxone.com',
  [EnvironmentName.NA1]: 'niceincontact.com',
  [EnvironmentName.NA2]: 'nicecxone-gov.com',
  [EnvironmentName.UK1]: 'niceincontact.com',
  [EnvironmentName.UK2]: 'nicecxone-sov1.uk',
  [EnvironmentName.ZA1]: 'nicecxone.com',
  [EnvironmentName.custom]: '',
};

export interface EnvironmentEndpoints {
  authorize: string;
  chat: string;
  gateway: string;
  name: string;
}

export function buildEnvironmentEndpoints(
  environment: EnvironmentName,
): EnvironmentEndpoints {
  if (!(environment in EnvironmentName)) {
    throw new ChatSDKError(`Unknown environment: ${environment}`);
  }

  if (environment === EnvironmentName.custom) {
    throw new ChatSDKError(
      'The custom environment cannot be constructed using the built-in configuration',
    );
  }

  const publicDNS = EnvironmentToPublicDNS[environment];
  const chat = `https://channels-de-${environment}.${publicDNS}`.toLowerCase();
  const gateway =
    `wss://chat-gateway-de-${environment}.${publicDNS}`.toLowerCase();
  const name = environment;
  const authorize =
    `https://digital-oauth-de-${environment}.${publicDNS}`.toLowerCase();

  return {
    chat,
    name,
    gateway,
    authorize,
  };
}
