import { handleRefreshToken } from '../authorization/handleRefreshToken';

import { refreshThirdPartyToken } from './refreshThirdPartyToken';
import { ThirdPartyToken } from './ThirdPartyToken';
import { setThirdPartyToken } from './thirdPartyTokenStorage';

export function handleNewThirdPartyToken(token: ThirdPartyToken): void {
  setThirdPartyToken(token);
  handleRefreshToken({ expiresIn: token.expires_in }, refreshThirdPartyToken);
}
