import { SendMessageEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/SendMessage';

import { sendChatEvent } from '../event/sendChatEvent';
import { WebSocketClient } from '../sockets/WebSocketClient';

import {
  isSendMessageSuccessEventData,
  MessageSuccessEventData,
} from './MessageSuccessEventData';
import { SendMessageFailedError } from './SendMessageFailedError';
import { createSendMessagePayloadData } from './sendMessagePayload';

export const sendMessage = async (
  messageData: SendMessageEventData,
  websocketClient: WebSocketClient | null,
): Promise<MessageSuccessEventData> => {
  const sendMessagePayloadData = createSendMessagePayloadData(messageData);

  const response = await sendChatEvent(sendMessagePayloadData, websocketClient);

  if (isSendMessageSuccessEventData(response)) {
    return response;
  }

  throw new SendMessageFailedError('Send message failed', { response });
};
