import { agentTypingEnded, agentTypingStarted } from '../../agent/agentTyping';
import { assignedAgentChanged } from '../../agent/assignedAgentChanged';
import { contactCreated } from '../../contact/contactCreated';
import { contactStatusChanged } from '../../contact/contactStatusChanged';
import { contactToRoutingQueueAssignmentChanged } from '../../contact/contactToRoutingQueueAssignmentChanged';
import { livechatRecovered } from '../../thread/livechatThread/livechatRecovered';
import { threadRecovered } from '../../thread/threadRecovered';
import { ChatEvent, ChatEventData } from '../ChatEvent';
import { EventMiddleware } from '../ChatEventMiddleware';

const eventTypeMap: Record<string, EventMiddleware> = {
  [ChatEvent.SENDER_TYPING_STARTED]: agentTypingStarted,
  [ChatEvent.SENDER_TYPING_ENDED]: agentTypingEnded,
  [ChatEvent.CASE_INBOX_ASSIGNEE_CHANGED]: assignedAgentChanged,
  [ChatEvent.CASE_CREATED]: contactCreated,
  [ChatEvent.CASE_STATUS_CHANGED]: contactStatusChanged,
  [ChatEvent.CASE_TO_ROUTING_QUEUE_ASSIGNMENT_CHANGED]:
    contactToRoutingQueueAssignmentChanged,
  [ChatEvent.LIVECHAT_RECOVERED]: livechatRecovered,
  [ChatEvent.THREAD_RECOVERED]: threadRecovered,
} as const;

export function chatEventMapper(event: ChatEventData): ChatEventData | null {
  if (event.type && eventTypeMap[event.type] !== undefined) {
    return eventTypeMap[event.type](event);
  }

  return event;
}
