import { BrandId } from '@brandembassy/ts-types-platform/dist/brand/BrandId';
import { ChannelId } from '@brandembassy/ts-types-platform/dist/channel/ChannelId';

import {
  EnvironmentName,
  buildEnvironmentEndpoints,
} from '../env/Environments';

import { ChannelAvailabilityOptions } from './ChannelAvailabilityOptions';
import {
  ChannelAvailabilityResponse,
  fetchChannelAvailability,
} from './fetchChannelAvailability';

/**
 * Get channel availability
 * Returns the availability status of a channel, indicating whether it is online or offline.
 * @see {@link ChannelAvailabilityResponse}
 */
export async function getChannelAvailability(
  brandId: BrandId,
  channelId: ChannelId,
  options: ChannelAvailabilityOptions,
): Promise<ChannelAvailabilityResponse> {
  const { httpHeaders, appName, appVersion, customEnvironment, environment } =
    options;

  const additionalHttpHeaders = {
    ...(httpHeaders ?? {}),
    ...(appVersion ? { 'x-app-version': String(appVersion) } : {}),
  };

  const chatEndpoint =
    environment === EnvironmentName.custom
      ? customEnvironment.chat
      : buildEnvironmentEndpoints(environment).chat;

  return fetchChannelAvailability(
    brandId,
    channelId,
    chatEndpoint,
    additionalHttpHeaders,
    appName,
  );
}
