import { SendMessageEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/SendMessage';
import { AttachmentUpload } from '@brandembassy/ts-types-chat/dist/message/AttachmentUpload';
import { BrandId } from '@brandembassy/ts-types-platform/dist/brand/BrandId';
import { ChannelId } from '@brandembassy/ts-types-platform/dist/channel/ChannelId';
import { MessageType } from '@brandembassy/ts-types-platform/dist/message/MessageType';
import { ThreadIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/thread/ThreadIdOnExternalPlatform';

import { getBrowserFingerprint } from '../browserFingerprint/getBrowserFingerprint';
import { ChatSDKError } from '../error/ChatSDKError';
import { SendMessageOptions } from '../thread/SendMessageOptions';
import { generateId } from '../utils/generateId';
import { getBrandIdAndChannelId } from '../utils/getBrandIdAndChannelId';

import { createPayloadForAttachmentUpload } from './createPayloadForAttachmentUpload';
import {
  isUploadFailResponse,
  isUploadSuccessResponse,
  uploadAttachment,
} from './uploadAttachment';
import { UploadAttachmentError } from './UploadAttachmentError';

export const createAttachmentPayload = async (
  file: File,
  brandId: BrandId,
  channelId: ChannelId,
  abortSignal?: AbortSignal,
): Promise<AttachmentUpload> => {
  const attachment = await createPayloadForAttachmentUpload(file);
  const uploadAttachmentResponse = await uploadAttachment(
    brandId,
    channelId,
    attachment,
    abortSignal,
  );

  if (isUploadSuccessResponse(uploadAttachmentResponse)) {
    return {
      url: uploadAttachmentResponse.fileUrl,
      friendlyName: attachment.name,
    };
  }

  if (isUploadFailResponse(uploadAttachmentResponse)) {
    throw new UploadAttachmentError('Upload attachment failed', {
      response: uploadAttachmentResponse,
    });
  }

  throw new ChatSDKError(
    `Unknown file upload response (${uploadAttachmentResponse})`,
  );
};

export const isAttachmentUpload = (
  files: FileList | Array<File> | Array<AttachmentUpload>,
): files is Array<AttachmentUpload> => {
  const filesToTest: Array<File | AttachmentUpload> = Array.from<
    File | AttachmentUpload
  >(files);

  return (filesToTest as Array<AttachmentUpload>).every(
    (file) => 'url' in file && 'friendlyName' in file,
  );
};

export const createTemporaryAttachmentsUpload = async (
  files: FileList | Array<File>,
  brandId: BrandId,
  channelId: ChannelId,
  abortSignal?: AbortSignal,
): Promise<Array<AttachmentUpload>> =>
  Promise.all<AttachmentUpload>(
    Array.from(files).map((file) =>
      createAttachmentPayload(file, brandId, channelId, abortSignal),
    ),
  );

export const createAttachmentUploadMessageData = async (
  files: FileList | Array<File> | Array<AttachmentUpload>,
  threadIdOnExternalPlatform: ThreadIdOnExternalPlatform,
  options: SendMessageOptions = {},
): Promise<SendMessageEventData> => {
  const { brandId, channelId } = getBrandIdAndChannelId();

  try {
    const attachments = isAttachmentUpload(files)
      ? files
      : await createTemporaryAttachmentsUpload(files, brandId, channelId);

    const {
      messageId = generateId(),
      browserFingerprint = getBrowserFingerprint(),
    } = options;

    return {
      messageContent: {
        type: MessageType.TEXT,
        payload: {
          text: '',
        },
      },
      attachments,
      browserFingerprint,
      thread: {
        idOnExternalPlatform: threadIdOnExternalPlatform,
      },
      idOnExternalPlatform: messageId,
      consumer: {
        customFields: [],
      },
      consumerContact: {
        customFields: [],
      },
    };
  } catch (error: unknown) {
    if (error instanceof UploadAttachmentError) {
      throw error;
    }

    if (error instanceof Error) {
      throw new ChatSDKError(
        `Send attachment failed because of (${error.message})`,
      );
    }

    throw new ChatSDKError(`Unknown error during file upload`);
  }
};
