import { sendChatEvent } from '../event/sendChatEvent';
import { WebSocketClient } from '../sockets/WebSocketClient';

import {
  isSendMessageSuccessEventData,
  MessageSuccessEventData,
} from './MessageSuccessEventData';
import { SendMessageFailedError } from './SendMessageFailedError';
import { createSendOfflineMessagePayloadData } from './sendOfflineMessagePayload';

export interface OfflineMessageData {
  email: string;
  message: string;
  name: string;
}

export const sendOfflineMessage = async (
  offlineMessageData: OfflineMessageData,
  websocketClient: WebSocketClient | null,
): Promise<MessageSuccessEventData> => {
  const offlineMessagePayloadData =
    createSendOfflineMessagePayloadData(offlineMessageData);

  const response = await sendChatEvent(
    offlineMessagePayloadData,
    websocketClient,
  );

  if (isSendMessageSuccessEventData(response)) {
    return response;
  }

  throw new SendMessageFailedError('Send offline message failed', response);
};
