import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { SendMessageEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/SendMessage';

import { EventPayloadData } from '../sockets/EventPayload';

export const createSendMessagePayloadData = (
  eventData: SendMessageEventData,
): EventPayloadData<SendMessageEventData> => ({
  eventType: AwsInputEventType.SEND_MESSAGE,
  data: eventData,
});
