import { AccessToken } from '@brandembassy/ts-types-chat/dist/authorization/AccessToken';

import { isNull } from '../utils/is';
import { SDKVariableStorage } from '../variables/VariableStorage';

export function setAccessToken(accessToken: AccessToken): void {
  SDKVariableStorage.ACCESS_TOKEN = accessToken.token;
  SDKVariableStorage.ACCESS_TOKEN_EXPIRES_IN = accessToken.expiresIn;
}

/**
 * @deprecated Use ThirdParty Token instead
 */
export function getAccessToken(): AccessToken | null {
  const token = SDKVariableStorage.ACCESS_TOKEN ?? null;
  const expiresIn = SDKVariableStorage.ACCESS_TOKEN_EXPIRES_IN ?? null;

  if (isNull(token) || isNull(expiresIn)) {
    return null;
  }

  return {
    token,
    expiresIn: Number(expiresIn),
  };
}

export function resetAccessToken(): void {
  SDKVariableStorage.ACCESS_TOKEN = undefined;
  SDKVariableStorage.ACCESS_TOKEN_EXPIRES_IN = undefined;
}
