import { TypingEventData } from '@brandembassy/ts-types-platform/dist/user/typing/TypingEventData';

import { ChatEvent, ChatEventData } from '../event/ChatEvent';

export type AgentTypingEndedData = TypingEventData;

export interface AgentTypingEndedEvent extends ChatEventData {
  data: AgentTypingEndedData;
  type: typeof ChatEvent.AGENT_TYPING_ENDED;
}

export function isAgentTypingEndedEvent(
  event: ChatEventData,
): event is AgentTypingEndedEvent {
  return (
    event.type === ChatEvent.AGENT_TYPING_ENDED &&
    (event as AgentTypingEndedEvent).data?.thread?.idOnExternalPlatform !==
      undefined
  );
}
