import { MessageSentData } from '@brandembassy/ts-types-platform/dist/message/MessageSentEvent';
import { PushUpdateEventType } from '@brandembassy/ts-types-platform/dist/pushUpdate/PushUpdateEventType';

import { ChatEventData } from '../event/ChatEvent';

export interface MessageSentEvent extends ChatEventData {
  data: MessageSentData;
  type: PushUpdateEventType.MESSAGE_SENT;
}

export function isMessageSentEvent(event: unknown): event is MessageSentEvent {
  return (event as MessageSentEvent).type === PushUpdateEventType.MESSAGE_SENT;
}
