import { ChatSDKError } from '../error/ChatSDKError';

const MAXIMUM_REQUEST_COUNT: number = 100;
const TIME_SLOT_IN_SECONDS: number = 5;
let requestRateLimitCounter: number = 0;
let requestCounterIntervalId: ReturnType<typeof setInterval> | null = null;

export class MaximumRequestCountReachError extends Error {
  name = 'MaximumRequestCountReached';
  message = 'Maximum request count reached';
}

export function resetLimitCounter(): void {
  requestRateLimitCounter = 0;
}

export function requestRateLimit(): void {
  requestRateLimitCounter += 1;

  if (requestCounterIntervalId === null) {
    requestCounterIntervalId = setInterval(
      resetLimitCounter,
      TIME_SLOT_IN_SECONDS * 1000,
    );
  }

  if (requestRateLimitCounter > MAXIMUM_REQUEST_COUNT) {
    throw new ChatSDKError(new MaximumRequestCountReachError());
  }
}
