import { ChatEvent, ChatEventData } from '../event/ChatEvent';

import { ContactCreatedChatEvent } from './contactCreated';

export function isContactCreatedEvent(
  event: ChatEventData,
): event is ContactCreatedChatEvent {
  return (
    event.type === ChatEvent.CONTACT_CREATED &&
    (event as ContactCreatedChatEvent).data?.case?.id !== undefined
  );
}
