import { MessageReadChangedData } from '@brandembassy/ts-types-platform/dist/message/MessageReadChangedEvent';
import { PushUpdateEventType } from '@brandembassy/ts-types-platform/dist/pushUpdate/PushUpdateEventType';

import { ChatEventData } from '../event/ChatEvent';

export interface MessageReadChangedEvent extends ChatEventData {
  data: MessageReadChangedData;
  type: PushUpdateEventType.MESSAGE_READ_CHANGED;
}

export function isMessageReadChangedEvent(
  event: unknown,
): event is MessageReadChangedEvent {
  return (
    (event as MessageReadChangedEvent).type ===
    PushUpdateEventType.MESSAGE_READ_CHANGED
  );
}
