import { AwsChatWindowEventActionRoute } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsChatWindowEvent';
import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import {
  AwsInputEvent,
  AwsInputEventData,
  ConsumerIdentityInput,
  DestinationInput,
  VisitInput,
  VisitorInput,
} from '@brandembassy/ts-types-chat/dist/awsEvents/AwsInputEvent';

import { createCustomerIdentityPayload } from '../customer/customerIdentityPayload';
import { ChatSDKError } from '../error/ChatSDKError';
import { generateId } from '../utils/generateId';
import { getBrandIdAndChannelId } from '../utils/getBrandIdAndChannelId';
import { isUndefined } from '../utils/is';
import { SDKVariableStorage } from '../variables/VariableStorage';

export type EventActionRoute = AwsChatWindowEventActionRoute;
export type EventPayload<D extends AwsInputEventData> = AwsInputEvent<
  AwsInputEventType,
  D
>;

export interface Event<D extends AwsInputEventData> {
  action: EventActionRoute;
  eventId: string;
  payload: EventPayload<D>;
}

export interface EventPayloadData<D extends AwsInputEventData> {
  consumerIdentity?: ConsumerIdentityInput;
  data: D;
  destination?: DestinationInput;
  eventType: AwsInputEventType;
  visit?: VisitInput;
  visitor?: VisitorInput;
}

export function createEventPayload<D extends AwsInputEventData>(
  eventPayloadData: EventPayloadData<D>,
): EventPayload<D> {
  const storedDestinationId = SDKVariableStorage.DESTINATION;
  const storedVisitId = SDKVariableStorage.VISIT_ID;
  const storedVisitorId = SDKVariableStorage.VISITOR_ID;

  const {
    eventType,
    data,
    consumerIdentity = createCustomerIdentityPayload(),
    destination = storedDestinationId ? { id: storedDestinationId } : {},
    visitor = storedVisitorId ? { id: storedVisitorId } : {},
    visit = storedVisitId ? { id: storedVisitId } : {},
  } = eventPayloadData;
  const { brandId, channelId } = getBrandIdAndChannelId();

  if (isUndefined(eventType)) {
    throw new ChatSDKError(
      `Cannot create an event payload because of missing eventType (${eventType})`,
    );
  }

  return {
    eventType,
    brand: {
      id: Number(brandId),
    },
    channel: {
      id: channelId,
    },
    consumerIdentity,
    data,
    destination,
    visitor,
    visit,
  };
}

export function createEvent<D extends AwsInputEventData>(
  payload: EventPayload<D>,
  eventId: string = generateId(),
  actionRoute: EventActionRoute = AwsChatWindowEventActionRoute.CHAT_WINDOW_EVENT,
): Event<D> {
  return {
    action: actionRoute,
    eventId,
    payload,
  };
}
