import { ChannelInfo } from '@brandembassy/ts-types-chat/dist/channel/ChannelInfo';
import { BrandId } from '@brandembassy/ts-types-platform/dist/brand/BrandId';
import { ChannelId } from '@brandembassy/ts-types-platform/dist/channel/ChannelId';

import { getBrowserLanguage } from '../browserFingerprint/getBrowserFingerprint';
import {
  EnvironmentName,
  buildEnvironmentEndpoints,
} from '../env/Environments';

import { ChannelInfoOptions } from './ChannelInfoOptions';
import { fetchChannelInfo } from './fetchChannelInfo';

/**
 * Get channel info
 * Returns channel info like feature toggle status, translations, file upload restrictions, theme color settings etc.
 * @see {@link ChannelInfo}
 */
export async function getChannelInfo(
  brandId: BrandId,
  channelId: ChannelId,
  language = getBrowserLanguage(),
  options: ChannelInfoOptions,
): Promise<ChannelInfo> {
  const { httpHeaders, environment, customEnvironment, appName, appVersion } =
    options;

  const additionalHttpHeaders = {
    ...(httpHeaders ?? {}),
    ...(appVersion ? { 'x-app-version': String(appVersion) } : {}),
  };

  const chatEndpoint =
    environment === EnvironmentName.custom
      ? customEnvironment.chat
      : buildEnvironmentEndpoints(environment).chat;

  return fetchChannelInfo(
    brandId,
    channelId,
    language,
    chatEndpoint,
    additionalHttpHeaders,
    appName,
  );
}
