import { AwsResponseEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';

import { ChatEventData } from '../event/ChatEvent';

import { AuthorizeConsumerEventSuccessResponse } from './AuthorizeConsumerEventResponse';

export const isAuthSuccessEvent = (
  payload: ChatEventData,
): payload is AuthorizeConsumerEventSuccessResponse => {
  const status = (payload as AuthorizeConsumerEventSuccessResponse)?.data
    ?.status;
  const type = payload?.type;

  return (
    type === AwsResponseEventType.CONSUMER_AUTHORIZED && status === 'success'
  );
};
