import { ChatSDKError } from '../error/ChatSDKError';

import { UploadFailResponse } from './uploadAttachment';

interface UploadAttachmentErrorData {
  response: UploadFailResponse;
}

export class UploadAttachmentError extends ChatSDKError {
  data: UploadAttachmentErrorData;

  constructor(message: string, data: UploadAttachmentErrorData) {
    super(message);
    this.data = data;
  }
}
