import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { LoadMoreMessagesEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/LoadMoreMessages';

import { EventPayloadData } from '../sockets/EventPayload';

export const createLoadMoreMessagesPayloadData = (
  eventData: LoadMoreMessagesEventData,
): EventPayloadData<LoadMoreMessagesEventData> => ({
  eventType: AwsInputEventType.LOAD_MORE_MESSAGES,
  data: eventData,
});
