import { CustomerIdentityIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/endUser/EndUserIdentityId';

import { ChatSDKError } from '../error/ChatSDKError';
import { ERROR_CANNOT_FETCH_TRANSACTION_TOKEN } from '../error/errorMessages';
import { postJSON } from '../utils/fetchJSON';
import { isNil } from '../utils/is';
import { SDKVariableStorage } from '../variables/VariableStorage';

import {
  cacheTransactionToken,
  getCachedTransactionToken,
} from './cachedTransactionToken';
import { getAuthorizeUrl } from './getAuthorizeUrl';
import { getSecuredSessionType } from './getSecuredSessionType';
import { getIdentityToken } from './identityToken';
import { SecureSessions } from './SecureSessionsType';
import { TransactionTokenResponse } from './TransactionToken';

interface ThirdPartyAuthorizationPayload {
  authorization_code: string;
  code_verifier?: string;
  grant_type: 'authorization_code';
}

interface ThirdPartyAuthorizePayload {
  thirdParty: ThirdPartyAuthorizationPayload;
}

interface AnonymousAuthorizationPayload {
  customerIdentity: {
    idOnExternalPlatform: CustomerIdentityIdOnExternalPlatform;
  };
}

type AuthorizePayload =
  | ThirdPartyAuthorizePayload
  | AnonymousAuthorizationPayload;

function getAuthorizePayload(
  authorizationCode?: string,
  customerIdentityIdOnExternalPlatform?: CustomerIdentityIdOnExternalPlatform | null,
): AuthorizePayload {
  const payload = {} as AuthorizePayload;

  if (authorizationCode) {
    (payload as ThirdPartyAuthorizePayload).thirdParty = {
      authorization_code: authorizationCode,
      grant_type: 'authorization_code',
    };
  }

  if (!isNil(customerIdentityIdOnExternalPlatform)) {
    (payload as AnonymousAuthorizationPayload).customerIdentity = {
      idOnExternalPlatform: customerIdentityIdOnExternalPlatform,
    };
  }

  return payload;
}

export const getTransactionToken = async (
  authorizationCode?: string,
  customerIdentityIdOnExternalPlatform?: CustomerIdentityIdOnExternalPlatform | null,
  cookieStorageFallback: boolean = false,
  useCache: boolean = false,
): Promise<
  Partial<TransactionTokenResponse> & {
    cached?: boolean;
  }
> => {
  // Get cached token if available
  if (useCache) {
    const cachedTransactionToken = getCachedTransactionToken();
    if (cachedTransactionToken) {
      return {
        accessToken: cachedTransactionToken,
        identityToken: getIdentityToken() ?? undefined,
        cached: true,
      };
    }
  }

  // If 3rd party cookies are blocked, we need to "activate" the fallback on the server side by query parameter
  const sendStorageFallbackQueryParameter =
    cookieStorageFallback &&
    getSecuredSessionType() === SecureSessions.SECURED_COOKIES;

  // If we already have identity token and are in fallback mode,
  // send authorization header to receive correct identity token
  const identityToken = getIdentityToken();
  const sendFallbackAuthorization =
    sendStorageFallbackQueryParameter && identityToken;

  // Request transaction token
  const authorizeUrl = getAuthorizeUrl(sendStorageFallbackQueryParameter);
  const data = await postJSON<TransactionTokenResponse>(
    authorizeUrl,
    getAuthorizePayload(
      authorizationCode,
      customerIdentityIdOnExternalPlatform,
    ),
    {
      credentials: 'include',
      headers: {
        ...SDKVariableStorage.REQUEST_METADATA?.httpHeaders,
        ...(sendFallbackAuthorization && {
          Authorization: `Bearer ${identityToken}`,
        }),
      },
    },
  );

  if (!data.accessToken) {
    throw new ChatSDKError(ERROR_CANNOT_FETCH_TRANSACTION_TOKEN, { data });
  }

  cacheTransactionToken(data.accessToken, data.expiresIn);

  return data;
};
