import { ChatEventData } from '../event/ChatEvent';
import { normalizeEvent } from '../event/normalizeEvent';

import { requestQueue } from './requestQueue';

export const parseWsMessageEvent = (
  event: CustomEvent,
): ChatEventData | undefined => {
  const eventDetail = event?.detail;

  if (!eventDetail) {
    return;
  }

  let eventData;

  try {
    eventData = JSON.parse(eventDetail.data);
  } catch (error) {
    return;
  }

  return normalizeEvent(eventData);
};

export const onMessageHandler = (event: ChatEventData): void => {
  const { id } = event;

  if (requestQueue.has(id)) {
    const resolve = requestQueue.get(id);

    if (typeof resolve === 'function') {
      resolve(event);
    }

    requestQueue.delete(id);
  }
};
