import { ChatSDKError } from '../error/ChatSDKError';
import { ChatEventData } from '../event/ChatEvent';
import { normalizeEvent } from '../event/normalizeEvent';
import { isMessageCreatedEvent } from '../message';
import { getMessageWithFullContent } from '../message/getMessageFullContent';
import { WebSocketClient } from '../sockets/WebSocketClient';
import { getJSON } from '../utils/fetchJSON';
import { isNil } from '../utils/is';

import { isS3ChatEvent } from './isS3ChatEvent';

export const getEventData = async (
  eventData: ChatEventData | undefined,
  websocketClient: WebSocketClient | null,
): Promise<ChatEventData | null> => {
  if (isNil(eventData)) {
    return null;
  }

  if (!isS3ChatEvent(eventData) && !isMessageCreatedEvent(eventData)) {
    return eventData;
  }

  if (isMessageCreatedEvent(eventData)) {
    return getMessageWithFullContent(eventData, websocketClient);
  }

  if (isS3ChatEvent(eventData)) {
    const response = await getJSON<ChatEventData>(eventData.data.s3Object.url);

    return normalizeEvent(response);
  }

  throw new ChatSDKError('Failed to fetch additional data for event');
};
