import { SDKVariableStorage } from '../variables/VariableStorage';

import { ThirdPartyToken } from './ThirdPartyToken';

export function setThirdPartyToken(token: ThirdPartyToken): void {
  SDKVariableStorage.THIRD_PARTY_TOKEN = token;
}
export function getThirdPartyToken(): ThirdPartyToken | null {
  return SDKVariableStorage.THIRD_PARTY_TOKEN ?? null;
}
export function resetThirdPartyToken(): void {
  SDKVariableStorage.THIRD_PARTY_TOKEN = undefined;
}
