import { ChatEventData } from '../event/ChatEvent';
import { isUndefined } from '../utils/is';

export interface MessageSuccessEventData extends ChatEventData {
  id: string;
}
export interface MessageFailedEventData extends MessageSuccessEventData {
  error: {
    errorCode: string;
    errorMessage: string;
    transactionId: string;
  };
  id: string;
}

export function isSendMessageSuccessEventData(
  event: ChatEventData,
): event is MessageSuccessEventData {
  const hasEventId =
    isUndefined((event as MessageSuccessEventData)?.id) === false;

  const hasError =
    isUndefined((event as MessageFailedEventData).error) === false;

  return hasError === false && hasEventId;
}
