import { SDKVariableStorage } from '../variables/VariableStorage';

import { TransactionAccessToken } from './TransactionToken';

const TRANSACTION_TOKEN_CACHE_KEY = 'sdkTransactionToken';

export const getCachedTransactionToken = (): TransactionAccessToken | null => {
  if (!SDKVariableStorage.CACHE_STORAGE) {
    return null;
  }

  return SDKVariableStorage.CACHE_STORAGE.getItem(TRANSACTION_TOKEN_CACHE_KEY);
};

export const cacheTransactionToken = (
  token: TransactionAccessToken,
  expiresIn: number,
): void => {
  if (!SDKVariableStorage.CACHE_STORAGE) {
    return;
  }

  SDKVariableStorage.CACHE_STORAGE.setItem(
    TRANSACTION_TOKEN_CACHE_KEY,
    token,
    expiresIn * 1000,
  );
};

export const clearCachedTransactionToken = (): void => {
  if (!SDKVariableStorage.CACHE_STORAGE) {
    return;
  }

  SDKVariableStorage.CACHE_STORAGE.removeItem(TRANSACTION_TOKEN_CACHE_KEY);
};
