import { ChatSDKError } from '../error/ChatSDKError';

interface ArchiveThreadFailedErrorData {
  response: unknown;
}

export class ArchiveThreadFailedError extends ChatSDKError {
  data: ArchiveThreadFailedErrorData;

  constructor(message: string, data: ArchiveThreadFailedErrorData) {
    super(message);
    this.data = data;
  }
}
