let refreshTokenTimeoutId: NodeJS.Timeout | null = null;

export function getIdealTimeInSecondsForIssueRefreshToken(
  accessTokenExpiresIn: number,
): number {
  const idealTimeToRefreshAccessToken = Math.round(accessTokenExpiresIn * 0.9);

  if (idealTimeToRefreshAccessToken < 20) {
    return 20;
  }

  return idealTimeToRefreshAccessToken;
}

export function handleRefreshToken(
  token: { expiresIn: number },
  refreshTokenAction: () => Promise<void>,
): void {
  if (refreshTokenTimeoutId !== null) {
    clearTimeout(refreshTokenTimeoutId);
  }

  refreshTokenTimeoutId = setTimeout(
    refreshTokenAction,
    getIdealTimeInSecondsForIssueRefreshToken(token.expiresIn) * 1000,
  );
}
