import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { SendMessagePreviewEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/SendMessagePreview';
import { MessageType } from '@brandembassy/ts-types-platform/dist/message/MessageType';
import { ThreadIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/thread/ThreadIdOnExternalPlatform';

import { EventPayloadData } from '../sockets/EventPayload';

export const createSendMessagePreviewPayloadData = (
  threadIdOnExternalPlatform: ThreadIdOnExternalPlatform,
  text: string,
): EventPayloadData<SendMessagePreviewEventData> => ({
  eventType: AwsInputEventType.SEND_MESSAGE_PREVIEW,
  data: {
    thread: {
      idOnExternalPlatform: threadIdOnExternalPlatform,
    },
    messageContent: {
      payload: { text },
      type: MessageType.TEXT,
    },
  },
});
