import { AuthorizationTokenGeneratedPostbackData } from '@brandembassy/ts-types-chat/dist/awsEvents/AuthorizationTokenGenerated';

import { isObject } from '../utils/is';

export const isAuthorizationTokenGeneratedPostbackData = (
  data: unknown,
): data is AuthorizationTokenGeneratedPostbackData =>
  isObject(data) &&
  'authorizationToken' in (data as AuthorizationTokenGeneratedPostbackData);
