import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { JoinGroupChatEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/JoinGroupChat';

import { isChatSDKError } from '../error/isChatSDKError';
import { ChatEvent, ChatEventData } from '../event/ChatEvent';
import { sendChatEvent } from '../event/sendChatEvent';
import { EventPayloadData } from '../sockets/EventPayload';
import { WebSocketClient } from '../sockets/WebSocketClient';

import { JoinGroupChatFailedError } from './JoinGroupChatFailedError';

export function createJoinGroupChatPayloadData(
  code: string,
): EventPayloadData<JoinGroupChatEventData> {
  return {
    eventType: AwsInputEventType.JOIN_GROUP_CHAT,
    data: {
      invitation: {
        code,
      },
    },
  };
}

export function isJoinGroupChatSuccessResponse(
  response: ChatEventData,
): boolean {
  return response.type === ChatEvent.GROUP_CHAT_JOINED;
}

export async function sendJoinGroupChatEvent(
  joinGroupChatPayloadData: EventPayloadData<JoinGroupChatEventData>,
  wsClient: WebSocketClient | null,
): Promise<ChatEventData> {
  const response = await sendChatEvent(joinGroupChatPayloadData, wsClient);

  if (isJoinGroupChatSuccessResponse(response)) {
    return response;
  }

  throw new JoinGroupChatFailedError('Join Group chat failed', { response });
}

export function isJoinGroupChatFailedError(
  error: unknown,
): error is InstanceType<typeof JoinGroupChatFailedError> {
  return error instanceof JoinGroupChatFailedError && isChatSDKError(error);
}
