import { getAuthorizationTokenForUrl } from '../authorization/getAuthorizationTokenForUrl';
import { ChatSDKError } from '../error/ChatSDKError';
import { ChatEventData } from '../event/ChatEvent';
import { WebSocketClient } from '../sockets/WebSocketClient';

import {
  fetchMessageWithFullContent,
  getUrlPathForMessageWithFullContent,
} from './fetchMessageWithFullContent';
import { MessageCreatedEvent } from './isMessageCreatedEvent';

export async function getMessageWithFullContent(
  messageCreatedEvent: MessageCreatedEvent,
  websocketClient: WebSocketClient | null,
): Promise<ChatEventData> {
  if (
    messageCreatedEvent.data.message.hasAdditionalMessageContent === false ||
    websocketClient === null
  ) {
    return messageCreatedEvent;
  }

  try {
    const { brand, message, thread } = messageCreatedEvent.data;
    const messageUrl = getUrlPathForMessageWithFullContent(
      brand.id,
      message.id,
    );
    const authorizationToken = await getAuthorizationTokenForUrl(
      thread.idOnExternalPlatform,
      messageUrl,
      websocketClient,
    );
    const messageWithFullContent = await fetchMessageWithFullContent(
      message,
      brand.id,
      authorizationToken,
    );

    return {
      ...messageCreatedEvent,
      data: {
        ...messageCreatedEvent.data,
        message: messageWithFullContent,
      },
    };
  } catch (error) {
    throw new ChatSDKError('Failed to fetch additional message content', {
      error,
    });
  }
}
