import { CALLER_SERVICE_ID } from '@brandembassy/ts-types-platform/dist/http/headers';

import { CHAT_SDK_VERSION } from '../env/version';
import { generateId } from '../utils/generateId';
import { SDKVariableStorage } from '../variables/VariableStorage';

import { NetworkRequestMetadata } from './NetworkRequestMetadata';

interface DefaultHeaders {
  Accept: string;
  [CALLER_SERVICE_ID]: string;
  'Content-Type': string;
  'X-Trace-ID': string;
  'x-sdk-platform': string;
  'x-sdk-version': string;
}

export const getDefaultHeaders = (
  requestMetadata: NetworkRequestMetadata['httpHeaders'],
  callerServiceId?: string,
): DefaultHeaders => {
  const requestMetadataHeaders = requestMetadata ?? {};

  return {
    ...requestMetadataHeaders,
    // Default headers for the fetch requests
    Accept: 'application/json',
    'Content-Type': 'application/json',
    [CALLER_SERVICE_ID]: callerServiceId ?? SDKVariableStorage.APP_NAME ?? '',
    'X-Trace-ID': requestMetadataHeaders['x-trace-id'] ?? generateId(),
    'x-sdk-platform': 'web',
    'x-sdk-version': CHAT_SDK_VERSION,
  };
};
