import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { RefreshTokenData } from '@brandembassy/ts-types-chat/dist/awsEvents/RefreshToken';

import { EventPayloadData } from '../sockets/EventPayload';

export function createRefreshTokenPayload(
  token: string,
): EventPayloadData<RefreshTokenData> {
  return {
    eventType: AwsInputEventType.REFRESH_TOKEN,
    data: {
      accessToken: {
        token,
      },
    },
  };
}
