export function assertNonNullable<T>(
  value: T,
): asserts value is NonNullable<T> {
  if (typeof value === 'undefined' || value === null) {
    throw Error(`Expected non-nullish value, got ${value}`);
  }
}

//eslint-disable-next-line @typescript-eslint/no-explicit-any
export function assertIsCallable<T extends (...args: Array<any>) => any>(
  value: T,
): asserts value is T {
  if (typeof value !== 'function') {
    throw Error(`Expected a callable function, got ${value}`);
  }
}
