interface PromiseWithResolvers<T = unknown> {
  promise: Promise<T>;
  reject: (reason: unknown) => void;
  resolve: (value: T) => void;
}

export function getPromiseWithResolvers<T>(): PromiseWithResolvers<T> {
  let resolve, reject;
  const promise = new Promise((res, rej) => {
    resolve = res;
    reject = rej;
  });

  return { promise, resolve, reject } as unknown as PromiseWithResolvers<T>;
}
