import { ChatSDKError } from '../error/ChatSDKError';

import { createLocalFileReader } from './localFileReader';

export interface AttachmentPayload {
  mimeType: string;
  name: string;
  url: string;
}

export const createPayloadForAttachmentUpload = async (
  file: File,
): Promise<AttachmentPayload> => {
  const reader = await createLocalFileReader(file);

  if (reader.error !== null) {
    throw new ChatSDKError(
      `Cannot create payload for attachment upload because of error (${reader.error.message})`,
    );
  }

  if (typeof reader.result !== 'string') {
    throw new ChatSDKError(
      `Cannot create payload for attachment upload because of missing:
      reader result (${reader.result})`,
    );
  }

  return {
    url: reader.result,
    name: file.name,
    mimeType: file.type,
  };
};
