import { ChatSDKError } from '../error/ChatSDKError';
import { ChatEventData } from '../event/ChatEvent';

interface ThreadRecoverFailedErrorData {
  response: ChatEventData;
}

export class ThreadRecoverFailedError extends ChatSDKError {
  data: ThreadRecoverFailedErrorData;

  constructor(message: string, data: ThreadRecoverFailedErrorData) {
    super(message);
    this.data = data;
  }
}
