import { PushUpdateContext } from '@brandembassy/ts-types-platform/dist/pushUpdate/PushUpdateContext';

import { MessageFailedEventData } from '../message/MessageSuccessEventData';
import { isEmpty } from '../utils/is';

import { ChatEventData, ChatEventType } from './ChatEvent';

interface EventBase {
  createdAt: string;
}

interface EventDataWithPostback extends EventBase {
  postback: {
    data: Record<string, unknown>;
    eventType: ChatEventType;
  };
}

interface EventWithEventId extends EventBase {
  eventId: string;
}

interface EventWithData extends EventBase {
  context?: [] | PushUpdateContext;
  data: Record<string, unknown>;
}
interface EventWithType extends EventBase {
  eventType: ChatEventType;
}

const EMPTY_EVENT = {
  id: '',
  data: null,
  type: undefined,
  createdAt: new Date().toISOString(),
};

const hasData = (event: unknown): event is EventWithData =>
  'data' in (event as EventWithData);

const hasError = (event: unknown): event is MessageFailedEventData =>
  'error' in (event as MessageFailedEventData);

const hasEventId = (event: unknown): event is EventWithEventId =>
  'eventId' in (event as EventWithEventId);

const hasEventType = (event: unknown): event is EventWithType =>
  'eventType' in (event as EventWithType);

const isDataWithPostback = (
  eventDetailData: unknown,
): eventDetailData is EventDataWithPostback => {
  const postback = (eventDetailData as EventDataWithPostback)?.postback;

  return isEmpty(postback) === false;
};

export function normalizeEvent(event: unknown): ChatEventData {
  if (!hasEventId(event)) {
    return EMPTY_EVENT;
  }

  if (hasError(event)) {
    return {
      createdAt: event.createdAt,
      data: null,
      error: event.error,
      id: event.eventId,
    };
  }

  const eventType = hasEventType(event) ? event.eventType : undefined;

  if (hasData(event)) {
    return {
      createdAt: event.createdAt,
      context: event.context,
      data: event.data,
      id: event.eventId,
      type: eventType,
    };
  }

  if (isDataWithPostback(event)) {
    const {
      postback: { data: postbackData, eventType: postbackEventType },
      eventId: rawEventId,
      ...restEventData
    } = event;

    return {
      type: postbackEventType,
      data: {
        ...restEventData,
        ...postbackData,
      },
      createdAt: event.createdAt,
      id: rawEventId,
    };
  }

  const { eventId, ...eventRest } = event;

  return {
    data: undefined,
    ...eventRest,
    id: eventId,
    type: eventType,
    createdAt: event.createdAt,
  };
}
