import { AwsInputEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsInputEvent';

import { ChatSDKError } from '../error/ChatSDKError';
import { ChatEventData } from '../event/ChatEvent';
import { Event } from '../sockets/EventPayload';
import { WebSocketClient } from '../sockets/WebSocketClient';
import { generateId } from '../utils/generateId';
import { isEmpty, isNull } from '../utils/is';

import { requestQueue } from './requestQueue';

export const makeRequest = async (
  event: Event<AwsInputEventData>,
  wsClient: WebSocketClient | null,
): Promise<ChatEventData> => {
  if (isNull(wsClient)) {
    throw new ChatSDKError('WebSocketClient is not initialized');
  }
  if (isEmpty(event.eventId)) {
    event.eventId = generateId();
  }

  return new Promise<ChatEventData>((resolve) => {
    requestQueue.set(event.eventId, resolve);
    wsClient?.send(event);
  });
};
