import { CaseInboxAssigneeChangedData } from '@brandembassy/ts-types-platform/dist/post/case/CaseInboxAssigneeChangedEvent';

import { ChatEvent, ChatEventData } from '../event/ChatEvent';

export interface AssignedAgentChangedEvent extends ChatEventData {
  data: CaseInboxAssigneeChangedData;
  type: typeof ChatEvent.ASSIGNED_AGENT_CHANGED;
}

export function isAssignedAgentChangedEvent(
  event: ChatEventData,
): event is AssignedAgentChangedEvent {
  return (
    event.type === ChatEvent.ASSIGNED_AGENT_CHANGED &&
    (event as AssignedAgentChangedEvent).data?.case?.id !== undefined
  );
}
