import { AwsInputEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsInputEvent';

import { makeRequest } from '../request/makeRequest';
import { requestRateLimit } from '../request/requestRateLimit';
import {
  createEvent,
  createEventPayload,
  EventPayloadData,
} from '../sockets/EventPayload';
import { WebSocketClient } from '../sockets/WebSocketClient';

import { ChatEventData } from './ChatEvent';

/**
 * Send chat event
 * @param payloadData - payload data
 * @param webSocketClient - websocket client
 */
export async function sendChatEvent<D extends AwsInputEventData>(
  payloadData: EventPayloadData<D>,
  webSocketClient: WebSocketClient | null,
): Promise<ChatEventData> {
  requestRateLimit();

  const event = createEvent(createEventPayload(payloadData));

  return makeRequest(event, webSocketClient);
}
