import UAParser from 'ua-parser-js';

import {
  ApplicationType,
  BrowserFingerprint,
  DeviceType,
} from '@brandembassy/ts-types-chat/dist/user/BrowserFingerprint';

export interface BrowserFingerprintOptions {
  country?: string | null;
  ip?: string | null;
  language?: string;
  location?: string | null;
}

export const DEFAULT_LANGUAGE = 'en-US';

export const getBrowserLanguage = (): string => navigator.language;

export const getBrowserLocation = (): string =>
  Intl.DateTimeFormat().resolvedOptions().timeZone;

/**
 * Get Device type
 * @param deviceType - device type
 */
export function getDeviceType(deviceType?: string): DeviceType {
  switch (deviceType) {
    case 'mobile':
      return DeviceType.MOBILE;
    case 'tablet':
      return DeviceType.TABLET;
    default:
      return DeviceType.DESKTOP;
  }
}

/**
 * Get Customer Browser fingerprint
 * @param options - options
 */
export const getBrowserFingerprint = (
  options: BrowserFingerprintOptions = {},
): BrowserFingerprint => {
  const deviceInfoFromUserAgent = new UAParser(navigator.userAgent);

  const {
    country = '',
    location = getBrowserLocation(),
    language = getBrowserLanguage(),
    ip = null,
  } = options;

  return {
    browser: deviceInfoFromUserAgent.getBrowser().name ?? null,
    browserVersion: deviceInfoFromUserAgent.getBrowser().version ?? null,
    country,
    ip,
    language,
    location,
    os: deviceInfoFromUserAgent.getOS().name ?? null,
    osVersion: deviceInfoFromUserAgent.getOS().version ?? null,
    deviceType: getDeviceType(deviceInfoFromUserAgent.getDevice().type),
    applicationType: ApplicationType.BROWSER,
  };
};

export const getValidLanguage = (language: string): string => {
  const cleanedLanguage = language.replace(/_/g, '-').split(/[^a-z0-9-]/i)[0];

  try {
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    if (typeof Intl.getCanonicalLocales === 'function') {
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      return Intl.getCanonicalLocales(cleanedLanguage)[0];
    }

    return new Intl.Locale(cleanedLanguage).baseName;
  } catch (error) {
    return DEFAULT_LANGUAGE;
  }
};
