import { SendMessageEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/SendMessage';
import { MessageType } from '@brandembassy/ts-types-platform/dist/message/MessageType';

import { SendMessageFailedError } from '../message';
import { isEmpty } from '../utils/is';

export function assertNonEmptyTextMessageContent(
  messageData: SendMessageEventData,
): void {
  if (messageData.messageContent.type !== MessageType.TEXT) {
    return;
  }

  if (messageData.attachments.length > 0) {
    return;
  }

  if (isEmpty(messageData.messageContent.payload.text) === false) {
    return;
  }

  throw new SendMessageFailedError(
    'Message content cannot be empty for text message',
  );
}
