import { Message } from '../message';
import { isUndefined } from '../utils/is';
import { lastItemInArray } from '../utils/last';
import { SDKVariableStorage } from '../variables/VariableStorage';

export interface ScrollData {
  messages: Array<Message>;
  scrollToken: string;
}

export function storeThreadScrollData(scrollData: ScrollData): void {
  const oldestMessageDatetime = lastItemInArray(scrollData.messages)?.createdAt;

  const oldThreadData = SDKVariableStorage.THREAD_DATA ?? {};

  SDKVariableStorage.THREAD_DATA = {
    ...oldThreadData,
    scrollToken: scrollData.scrollToken,
    oldestMessageDatetime: isUndefined(oldestMessageDatetime)
      ? ''
      : oldestMessageDatetime,
  };
}
