import { CaseCreatedData } from '@brandembassy/ts-types-platform/dist/post/case/CaseCreatedEvent';

import { ChatEvent, ChatEventData } from '../event/ChatEvent';

export type ContactCreatedData = CaseCreatedData;

export interface ContactCreatedChatEvent extends ChatEventData {
  data: ContactCreatedData;
  type: typeof ChatEvent.CONTACT_CREATED;
}

export function contactCreated(event: ChatEventData): ChatEventData {
  return {
    ...event,
    type: ChatEvent.CONTACT_CREATED,
  };
}
