import { ContactRecipientsChangedData } from '@brandembassy/ts-types-platform/dist/groupChat/ContactRecipientsChangedEvent';
import { PushUpdateEventType } from '@brandembassy/ts-types-platform/dist/pushUpdate/PushUpdateEventType';

import { ChatEventData } from '../event/ChatEvent';

export interface ContactRecipientsChangedChatEvent extends ChatEventData {
  data: ContactRecipientsChangedData;
  type: PushUpdateEventType.CONTACT_RECIPIENTS_CHANGED;
}

export function isContactRecipientsChangedEvent(
  event: unknown,
): event is ContactRecipientsChangedChatEvent {
  return (
    (event as ContactRecipientsChangedChatEvent).type ===
    PushUpdateEventType.CONTACT_RECIPIENTS_CHANGED
  );
}
