import { ChatSDKError } from '../error/ChatSDKError';
import { isNil } from '../utils/is';
import { SDKVariableStorage } from '../variables/VariableStorage';

import { SecureSessions, SecureSessionsType } from './SecureSessionsType';

export const getSecuredSessionType = (): SecureSessionsType =>
  SDKVariableStorage.SECURED_SESSION ?? null;

export const shouldHandleCustomerIdForSecureSessions = (): boolean =>
  SDKVariableStorage.SECURED_SESSION === SecureSessions.ANONYMOUS;

export const isSecureSessions = (): boolean => getSecuredSessionType() !== null;

export const getSecureSessionsTypeFromOptions = (
  secureSessionsOption: unknown,
): SecureSessionsType => {
  if (isNil(secureSessionsOption)) {
    return null;
  }

  const secureSessionType = Object.values(SecureSessions).includes(
    secureSessionsOption as SecureSessions,
  );

  if (secureSessionType === false) {
    throw new ChatSDKError(
      `Expected a SecureSessions value, got ${secureSessionsOption}`,
    );
  }

  return secureSessionsOption as SecureSessions;
};
