import { AccessToken } from '@brandembassy/ts-types-chat/dist/authorization/AccessToken';
import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { ReconnectConsumerData } from '@brandembassy/ts-types-chat/dist/awsEvents/ReconnectConsumer';
import { VisitorId } from '@brandembassy/ts-types-platform/dist/visitor/VisitorId';

import { createCustomerIdentityPayload } from '../customer/customerIdentityPayload';
import { EventPayloadData } from '../sockets/EventPayload';
import { createVisitorPayload } from '../visitor/createVisitorPayload';

export function createReconnectPayloadData(
  accessToken: AccessToken,
  visitorId?: string,
): EventPayloadData<ReconnectConsumerData> {
  return {
    ...createVisitorPayload(visitorId as VisitorId),
    ...createCustomerIdentityPayload(),
    eventType: AwsInputEventType.RECONNECT_CONSUMER,
    data: {
      accessToken: {
        token: accessToken.token,
      },
    },
  };
}
