import { TypingEventData } from '@brandembassy/ts-types-platform/dist/user/typing/TypingEventData';

import { ChatEvent, ChatEventData } from '../event/ChatEvent';

export type AgentTypingStartedData = TypingEventData;

export interface AgentTypingStartedEvent extends ChatEventData {
  data: AgentTypingStartedData;
  type: typeof ChatEvent.AGENT_TYPING_STARTED;
}

export function isAgentTypingStartedEvent(
  event: ChatEventData,
): event is AgentTypingStartedEvent {
  return (
    event.type === ChatEvent.AGENT_TYPING_STARTED &&
    (event as AgentTypingStartedEvent).data?.thread?.idOnExternalPlatform !==
      undefined
  );
}
