import { isContactCreatedEvent } from '../contact/isContactCreatedEvent';
import { ChatEventData } from '../event/ChatEvent';

import { isRecoverSuccessPayload } from './isRecoverSuccessPayload';
import { storeContactId } from './storeContactId';

export function storeContactIdMiddleware(
  eventData: ChatEventData,
): ChatEventData {
  if (isContactCreatedEvent(eventData)) {
    const contactId = eventData.data.case.id;

    storeContactId(contactId);
  }

  if (isRecoverSuccessPayload(eventData)) {
    const contactId = eventData.data.contact.id;

    storeContactId(contactId);
  }

  return eventData;
}
