export function generateId(): string {
  return crypto.randomUUID ? crypto.randomUUID() : generateUuidFallback();
}

export function generateUuidFallback(): string {
  const randomValues = new Uint8Array(16);

  // Convert each byte to a hexadecimal string and join it to form the UUID
  return crypto.getRandomValues(randomValues).reduce((uuid, value, index) => {
    if ([4, 6, 8, 10].includes(index)) {
      uuid += '-';
    }

    return (
      uuid +
      (index === 6
        ? // eslint-disable-next-line no-bitwise
          (value & 0x0f) | 0x40
        : index === 8
        ? // eslint-disable-next-line no-bitwise
          (value & 0x3f) | 0x80
        : value
      )
        .toString(16)
        .padStart(2, '0')
    );
  }, '');
}
