import { MessageType } from '@brandembassy/ts-types-platform/dist/message/MessageType';

import { MessageContent } from './createMessageData';

export function getPostbackMessageContent(
  postback: string,
  messageText: string,
): MessageContent {
  return {
    payload: {
      text: messageText,
      postback, // Leaving this here for backwards compatibility
    },
    postback,
    type: MessageType.TEXT, // MessageType.POSTBACK is deprecated
  };
}
