export async function getErrorResponseData(
  response: Response,
): Promise<unknown> {
  let errorResponseData: unknown;
  let rawDataAsString: string | undefined;

  try {
    rawDataAsString = await response.text();
    errorResponseData = JSON.parse(rawDataAsString);
  } catch (error) {
    errorResponseData = rawDataAsString;
  }

  if (!errorResponseData) {
    errorResponseData = response.statusText;
  }

  return errorResponseData;
}
