import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { SendOfflineMessageEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/SendOfflineMessage';
import { CustomerIdentityIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/customer/CustomerIdentity';
import { MessageType } from '@brandembassy/ts-types-platform/dist/message/MessageType';

import { EventPayloadData } from '../sockets/EventPayload';

import { OfflineMessageData } from './sendOfflineMessage';

export const createSendOfflineMessagePayloadData = (
  offlineMessageData: OfflineMessageData,
): EventPayloadData<SendOfflineMessageEventData> => {
  const [lastName, ...names] = offlineMessageData.name.split(' ').reverse();
  const firstName = names.reverse().join(' ');

  const authorCustomerIdentity = {
    idOnExternalPlatform:
      offlineMessageData.email as CustomerIdentityIdOnExternalPlatform,
    firstName,
    lastName,
  };

  const data = {
    messageContent: {
      type: MessageType.TEXT,
      payload: {
        text: offlineMessageData.message,
      },
    },
    authorCustomerIdentity,
  };

  return {
    eventType: AwsInputEventType.SEND_OFFLINE_MESSAGE,
    data,
  };
};
