import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { UpdateThreadEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/UpdateThread';
import { ThreadIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/thread/ThreadIdOnExternalPlatform';

import { ChatEventData } from '../event/ChatEvent';
import { EventPayloadData } from '../sockets/EventPayload';
import { isUndefined } from '../utils/is';

export function createUpdateThreadPayloadData(
  threadIdOnExternalPlatform: ThreadIdOnExternalPlatform,
  threadName: string,
): EventPayloadData<UpdateThreadEventData> {
  return {
    eventType: AwsInputEventType.UPDATE_THREAD,
    data: {
      thread: {
        idOnExternalPlatform: threadIdOnExternalPlatform,
        threadName,
      },
    },
  };
}

export function isUpdateThreadSuccess(response: ChatEventData): boolean {
  return isUndefined(response.error);
}
