import { AuthorizationToken } from '@brandembassy/ts-types-chat/dist/authorization/AuthorizationToken';
import { ThreadIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/thread/ThreadIdOnExternalPlatform';

import { ChatSDKError } from '../error/ChatSDKError';
import { sendChatEvent } from '../event/sendChatEvent';
import { WebSocketClient } from '../sockets/WebSocketClient';

import { createGenerateAuthorizationTokenPayloadData } from './generateAuthorizationTokenPayload';
import { isAuthorizationTokenGeneratedPostbackData } from './isAuthorizationTokenGeneratedPostbackData';

export async function getAuthorizationTokenForUrl(
  threadIdOnExternalPlatform: ThreadIdOnExternalPlatform,
  url: string,
  websocketClient: WebSocketClient,
): Promise<AuthorizationToken> {
  const result = await sendChatEvent(
    createGenerateAuthorizationTokenPayloadData(
      threadIdOnExternalPlatform,
      url,
    ),
    websocketClient,
  );

  if (!isAuthorizationTokenGeneratedPostbackData(result?.data)) {
    throw new ChatSDKError(
      'Invalid response from generate authorization token (generateAuthorizationToken)',
      { result },
    );
  }

  const { authorizationToken } = result.data;

  return authorizationToken;
}
