import { ChatEventData } from '../event/ChatEvent';
import { isLoadMoreMessagesEventData } from '../message/loadMoreMessagesEventData';

import { isRecoverSuccessPayload } from './isRecoverSuccessPayload';
import { storeThreadScrollData } from './storeThreadScrollData';

export function storeThreadScrollDataMiddleware(
  eventData: ChatEventData,
): ChatEventData {
  if (isRecoverSuccessPayload(eventData)) {
    const { messages, messagesScrollToken } = eventData.data;

    storeThreadScrollData({
      messages,
      scrollToken: messagesScrollToken,
    });
  }

  if (isLoadMoreMessagesEventData(eventData)) {
    const { scrollToken, messages } = eventData.data;

    storeThreadScrollData({ scrollToken, messages });
  }

  return eventData;
}
