import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { CreateInvitationToGroupChatEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/InvitationToGroupChat';
import { CaseId } from '@brandembassy/ts-types-platform/dist/post/case/CaseId';

import { ChatEvent, ChatEventData } from '../event/ChatEvent';
import { sendChatEvent } from '../event/sendChatEvent';
import { EventPayloadData } from '../sockets/EventPayload';
import { WebSocketClient } from '../sockets/WebSocketClient';

import { CreateInvitationFailedError } from './CreateInvitationFailedError';

export function createCreateInvitationToGroupChatPayloadData(
  id: CaseId,
): EventPayloadData<CreateInvitationToGroupChatEventData> {
  return {
    eventType: AwsInputEventType.CREATE_GROUP_CHAT_INVITE,
    data: {
      contact: {
        id,
      },
    },
  };
}

export function isInvitationToGroupChatCreated(
  response: ChatEventData,
): boolean {
  return response.type === ChatEvent.GROUP_CHAT_INVITE_CREATED;
}

export async function sendCreateInvitationToGroupChatEvent(
  createInvitationPayloadData: EventPayloadData<CreateInvitationToGroupChatEventData>,
  wsClient: WebSocketClient | null,
): Promise<ChatEventData> {
  const response = await sendChatEvent(createInvitationPayloadData, wsClient);

  if (isInvitationToGroupChatCreated(response)) {
    return response;
  }

  throw new CreateInvitationFailedError('Create invitation failed', response);
}
