import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { RecoverThreadEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/RecoverThread';
import { ThreadIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/thread/ThreadIdOnExternalPlatform';

import { EventPayloadData } from '../../sockets/EventPayload';

export function createRecoverLivechatThreadPayloadData(
  threadIdOnExternalPlatform?: ThreadIdOnExternalPlatform,
): EventPayloadData<RecoverThreadEventData> {
  const emptyData = {
    eventType: AwsInputEventType.RECOVER_LIVECHAT,
    data: {},
  };

  if (!threadIdOnExternalPlatform) {
    return emptyData;
  }

  return {
    ...emptyData,
    data: {
      thread: { idOnExternalPlatform: threadIdOnExternalPlatform },
    },
  };
}
