import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { SendEmailInvitationToGroupChatEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/SendEmailInvitationToGroupChat';
import { CaseId } from '@brandembassy/ts-types-platform/dist/post/case/CaseId';

import { ChatEvent, ChatEventData } from '../event/ChatEvent';
import { sendChatEvent } from '../event/sendChatEvent';
import { EventPayloadData } from '../sockets/EventPayload';
import { WebSocketClient } from '../sockets/WebSocketClient';

import { SendEmailInvitationFailedError } from './SendEmailInvitationFailedError';

export function createSendEmailInvitationToGroupChatPayloadData(
  caseId: CaseId,
  invitationCode: string,
  email: string,
): EventPayloadData<SendEmailInvitationToGroupChatEventData> {
  return {
    eventType: AwsInputEventType.SEND_EMAIL_INVITE_TO_GROUP_CHAT,
    data: {
      contact: {
        id: caseId,
      },
      invitation: {
        code: invitationCode,
      },
      recipients: [
        {
          idOnExternalPlatform: email,
        },
      ],
    },
  };
}

export function isEmailInvitationToGroupChatSent(
  response: ChatEventData,
): boolean {
  return response.type === ChatEvent.GROUP_CHAT_INVITE_SENT;
}

export async function sendEmailInvitationToGroupChatEvent(
  createInvitationPayloadData: EventPayloadData<SendEmailInvitationToGroupChatEventData>,
  wsClient: WebSocketClient | null,
): Promise<ChatEventData> {
  const response = await sendChatEvent(createInvitationPayloadData, wsClient);

  if (isEmailInvitationToGroupChatSent(response)) {
    return response;
  }

  throw new SendEmailInvitationFailedError('Send Email Invitation failed', {
    response,
  });
}
