import { ThreadListFetchedPostbackData } from '@brandembassy/ts-types-chat/dist/awsEvents/ThreadListFetched';

import { isNull } from '../utils/is';

export const isThreadListFetchedPostbackData = (
  data: unknown,
): data is ThreadListFetchedPostbackData => {
  if (isNull(data)) {
    return false;
  }

  return 'threads' in (data as ThreadListFetchedPostbackData);
};
