import {
  ERROR_CODE_SDK_VERSION_NOT_SUPPORTED,
  SdkVersionNotSupported,
} from '../../error/SdkVersionNotSupported';
import { ChatEventData } from '../ChatEvent';

export function isSdkVersionNotSupportedEvent(event: unknown): boolean {
  return (
    (event as ChatEventData)?.error?.errorCode ===
    ERROR_CODE_SDK_VERSION_NOT_SUPPORTED
  );
}

export function sdkVersionCheck(event: ChatEventData): ChatEventData | null {
  if (isSdkVersionNotSupportedEvent(event)) {
    throw new SdkVersionNotSupported();
  }

  return event;
}
