import { AuthorizationToken } from '@brandembassy/ts-types-chat/dist/authorization/AuthorizationToken';
import { BrandId } from '@brandembassy/ts-types-platform/dist/brand/BrandId';
import { Message } from '@brandembassy/ts-types-platform/dist/message/Message';
import { MessageId } from '@brandembassy/ts-types-platform/dist/message/MessageId';

import { ChatSDKError } from '../error/ChatSDKError';
import { getDefaultHeaders } from '../request/defaultHeaders';
import { FetchOptions, getJSON } from '../utils/fetchJSON';
import { SDKVariableStorage } from '../variables/VariableStorage';

export function getUrlPathForMessageWithFullContent(
  brandId: BrandId,
  messageId: MessageId,
): string {
  return `/chat/1.0/brand/${brandId}/messages/${messageId}`;
}

export async function fetchMessageWithFullContent(
  message: Message,
  brandId: BrandId,
  authorizationToken: AuthorizationToken,
): Promise<Message> {
  const resource = `${
    SDKVariableStorage.ENDPOINT_CHAT
  }${getUrlPathForMessageWithFullContent(brandId, message.id)}`;

  const requestMetadataHttpHeaders =
    SDKVariableStorage.REQUEST_METADATA?.httpHeaders || {};

  const options: FetchOptions = {
    headers: {
      ...getDefaultHeaders(requestMetadataHttpHeaders),
      Authorization: `Bearer ${authorizationToken.token}`,
    },
  };

  try {
    return getJSON<Message>(resource, options);
  } catch (error) {
    throw new ChatSDKError('Failed to fetch message content.', { error });
  }
}
