import { ChannelAvailability } from '@brandembassy/ts-types-chat/dist/channel/ChannelInfo';
import { BrandId } from '@brandembassy/ts-types-platform/dist/brand/BrandId';
import { ChannelId } from '@brandembassy/ts-types-platform/dist/channel/ChannelId';

import { ChatSDKError } from '../error/ChatSDKError';
import { getDefaultHeaders } from '../request/defaultHeaders';
import { NetworkRequestMetadata } from '../request/NetworkRequestMetadata';
import { getJSON } from '../utils/fetchJSON';

export interface ChannelAvailabilityResponse {
  status: ChannelAvailability;
}

export async function fetchChannelAvailability(
  brandId: BrandId,
  channelId: ChannelId,
  chatEndpoint: string,
  requestMetadataHttpHeaders: NetworkRequestMetadata['httpHeaders'] = {},
  callerServiceId?: string,
): Promise<ChannelAvailabilityResponse> {
  const resource = `${chatEndpoint}/chat/1.0/brand/${brandId}/channel/${channelId}/availability`;
  const options = {
    headers: {
      ...getDefaultHeaders(requestMetadataHttpHeaders, callerServiceId),
    },
  };

  try {
    return getJSON<ChannelAvailabilityResponse>(
      resource,
      options,
      callerServiceId,
    );
  } catch (error) {
    throw new ChatSDKError('Failed to fetch channel availability.', { error });
  }
}
