export interface CustomerReconnectSuccessPayloadData {
  reconnected: true;
}

export function isCustomerReconnectSuccessPayloadData(
  payload: unknown,
): payload is CustomerReconnectSuccessPayloadData {
  if (typeof payload !== 'object' || payload === null) {
    return false;
  }

  if (!('reconnected' in payload)) {
    return false;
  }

  return payload.reconnected === true;
}
