import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { MessageSeenByConsumerEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/MessageSeenByConsumer';

import { EventPayloadData } from '../sockets/EventPayload';

export function createLastMessageSeenPayloadData(
  threadIdOnExternalPlatform: string,
): EventPayloadData<MessageSeenByConsumerEventData> {
  return {
    eventType: AwsInputEventType.MESSAGE_SEEN,
    data: {
      thread: { idOnExternalPlatform: threadIdOnExternalPlatform },
    },
  };
}
