import { SDKVariableStorage } from '../variables/VariableStorage';

// The same key as identity cookie name
const IDENTITY_TOKEN_KEY = 'cx_digital_wi';

export function storeIdentityToken(token: string): void {
  SDKVariableStorage.IDENTITY_TOKEN = token;
  if (SDKVariableStorage.STORAGE) {
    SDKVariableStorage.STORAGE.setItem(IDENTITY_TOKEN_KEY, token);
  }
}

export function getIdentityToken(): string | null {
  if (SDKVariableStorage.IDENTITY_TOKEN) {
    return SDKVariableStorage.IDENTITY_TOKEN;
  }

  if (SDKVariableStorage.STORAGE) {
    return SDKVariableStorage.STORAGE.getItem(IDENTITY_TOKEN_KEY);
  }

  return null;
}

export function removeIdentityToken(): void {
  SDKVariableStorage.IDENTITY_TOKEN = undefined;

  if (SDKVariableStorage.STORAGE) {
    SDKVariableStorage.STORAGE.removeItem(IDENTITY_TOKEN_KEY);
  }
}
