import { ChatSDKError } from '../error/ChatSDKError';

export const validateAndFilterHttpHeaders = (
  httpHeaders: Record<string | number, unknown> | undefined,
  onError?: (error: Error) => void,
): Record<string, string> => {
  if (!httpHeaders) {
    return {};
  }

  const validHttpHeaders = Object.entries(httpHeaders).reduce(
    (acc, entry) => validateAndFilterHeader(acc, entry, onError),
    {} as Record<string, string>,
  );

  return validHttpHeaders;
};

export const validateAndFilterHeader = (
  acc: Record<string, string>,
  [key, value]: [unknown, unknown],
  onError?: (error: Error) => void,
): Record<string, string> => {
  if (
    (typeof key !== 'string' && typeof key !== 'number') ||
    (typeof value !== 'string' && typeof value !== 'number')
  ) {
    onError?.(
      new ChatSDKError(
        `Invalid HTTP header: key "${key}" and value "${value}" must be strings or numbers.`,
      ),
    );

    return acc;
  }

  // Convert key to string
  const headerKey = String(key);

  // check for empty key
  if (headerKey.trim() === '') {
    onError?.(
      new ChatSDKError(
        `Invalid HTTP header: key "${headerKey}" must not be an empty string.`,
      ),
    );

    return acc;
  }

  const headerObject = new Headers();
  try {
    headerObject.append(headerKey, String(value));
  } catch (e) {
    onError?.(
      new ChatSDKError(
        `Invalid HTTP header: key "${headerKey}" and value "${value}" cannot be added to Headers.`,
      ),
    );

    return acc;
  }

  acc[headerKey] = String(value);

  return acc;
};
