import { ContactToRoutingQueueAssignmentChangedData } from '@brandembassy/ts-types-platform/dist/post/case/CaseToRoutingQueueAssignmentChangedEvent';

import { ChatEvent, ChatEventData } from '../event/ChatEvent';

export interface ContactToRoutingQueueAssignmentChangedChatEvent
  extends ChatEventData {
  data: ContactToRoutingQueueAssignmentChangedData;
  type: typeof ChatEvent.CONTACT_TO_ROUTING_QUEUE_ASSIGNMENT_CHANGED;
}

export function isContactToRoutingQueueAssignmentChangedEvent(
  event: ChatEventData,
): event is ContactToRoutingQueueAssignmentChangedChatEvent {
  return (
    event.type === ChatEvent.CONTACT_TO_ROUTING_QUEUE_ASSIGNMENT_CHANGED &&
    (event as ContactToRoutingQueueAssignmentChangedChatEvent).data?.case
      ?.id !== undefined
  );
}
