import { ChatEvent, ChatEventData } from '../event/ChatEvent';

import { ContactToRoutingQueueAssignmentChangedChatEvent } from './isContactToRoutingQueueAssignmentChangedEvent';

export function contactToRoutingQueueAssignmentChanged(
  event: ChatEventData,
): ContactToRoutingQueueAssignmentChangedChatEvent {
  return {
    ...(event as ContactToRoutingQueueAssignmentChangedChatEvent),
    type: ChatEvent.CONTACT_TO_ROUTING_QUEUE_ASSIGNMENT_CHANGED,
  };
}
