import { ThreadRecoveredPostbackData } from '@brandembassy/ts-types-chat/dist/awsEvents/ThreadRecovered';
import { PushUpdateEventFields } from '@brandembassy/ts-types-platform/dist/pushUpdate/PushUpdateEventFields';

import { ChatEventData } from '../event/ChatEvent';
import { chatEventMapper } from '../event/middleware/ChatEventMapper';
import { normalizeEvent } from '../event/normalizeEvent';

import { ThreadRecoveredChatEvent } from './isRecoverSuccessPayload';

export function threadRecovered(
  event: ChatEventData,
): ThreadRecoveredChatEvent {
  const normalizedContactHistoryItems = (
    (event.data as ThreadRecoveredPostbackData)
      .contactHistory as Array<PushUpdateEventFields>
  )
    .map(normalizeEvent)
    // to apply new event types for all contact history items
    .map(chatEventMapper)
    .filter((item): item is ChatEventData => Boolean(item));

  return {
    ...event,
    data: {
      ...(event.data as ThreadRecoveredPostbackData),
      contactHistory: normalizedContactHistoryItems,
    },
  };
}
