import { CustomerIdentityIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/customer/CustomerIdentity';

import { isNil } from '../utils/is';

export function isCustomerIdentityIdValid(
  customerIdentityIdOnExternalPlatform:
    | CustomerIdentityIdOnExternalPlatform
    | undefined,
): customerIdentityIdOnExternalPlatform is CustomerIdentityIdOnExternalPlatform {
  return (
    !isNil(customerIdentityIdOnExternalPlatform) &&
    customerIdentityIdOnExternalPlatform !== ''
  );
}
