import { ChannelInfo } from '@brandembassy/ts-types-chat/dist/channel/ChannelInfo';
import { BrandId } from '@brandembassy/ts-types-platform/dist/brand/BrandId';
import { ChannelId } from '@brandembassy/ts-types-platform/dist/channel/ChannelId';

import { getBrowserLanguage } from '../browserFingerprint/getBrowserFingerprint';
import { ChatSDKError } from '../error/ChatSDKError';
import { getDefaultHeaders } from '../request/defaultHeaders';
import { NetworkRequestMetadata } from '../request/NetworkRequestMetadata';
import { FetchOptions, getJSON } from '../utils/fetchJSON';

export async function fetchChannelInfo(
  brandId: BrandId,
  channelId: ChannelId,
  language = getBrowserLanguage(),
  chatEndpoint: string,
  requestMetadataHttpHeaders: NetworkRequestMetadata['httpHeaders'] = {},
  callerServiceId?: string,
): Promise<ChannelInfo> {
  const languageLocale = `${language}`.split('-').join('_');
  const resource = `${chatEndpoint}/chat/1.0/brand/${brandId}/channel/${channelId}?locale=${languageLocale}`;

  const options: FetchOptions = {
    headers: {
      ...getDefaultHeaders(requestMetadataHttpHeaders, callerServiceId),
    },
  };

  try {
    return getJSON<ChannelInfo>(resource, options, callerServiceId);
  } catch (error) {
    throw new ChatSDKError('Failed to fetch channel info.', { error });
  }
}
