import { AwsResponseEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { SetPositionInQueuePayloadData } from '@brandembassy/ts-types-chat/dist/awsEvents/SetPositionInQueuePayload';
import { EventId } from '@brandembassy/ts-types-platform/dist/pushUpdate/EventId';

import { ChatEventData } from '../../event/ChatEvent';
import { isEmpty } from '../../utils/is';

export interface SetPositionInQueueChatEvent extends ChatEventData {
  data: SetPositionInQueuePayloadData;
  id: EventId;
  type: AwsResponseEventType.SET_POSITION_IN_QUEUE;
}

export const isSetPositionInQueueEvent = (
  event: unknown,
): event is SetPositionInQueueChatEvent => {
  const setPositionInQueueEvent = event as SetPositionInQueueChatEvent;

  return (
    Number.isInteger(setPositionInQueueEvent?.data.positionInQueue) &&
    isEmpty(setPositionInQueueEvent?.id) === false &&
    setPositionInQueueEvent?.type === AwsResponseEventType.SET_POSITION_IN_QUEUE
  );
};
