import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { SenderTypingEndedEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/SenderTypingEnded';
import { SenderTypingStartedEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/SenderTypingStarted';
import { ThreadIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/thread/ThreadIdOnExternalPlatform';

import { EventPayloadData } from '../sockets/EventPayload';

export function createStartTypingEventPayloadData(
  threadIdOnExternalPlatform: ThreadIdOnExternalPlatform,
): EventPayloadData<SenderTypingStartedEventData> {
  return {
    eventType: AwsInputEventType.SENDER_TYPING_STARTED,
    data: {
      thread: {
        idOnExternalPlatform: threadIdOnExternalPlatform,
      },
    },
  };
}

export function createStopTypingEventPayloadData(
  threadIdOnExternalPlatform: ThreadIdOnExternalPlatform,
): EventPayloadData<SenderTypingEndedEventData> {
  return {
    eventType: AwsInputEventType.SENDER_TYPING_ENDED,
    data: {
      thread: {
        idOnExternalPlatform: threadIdOnExternalPlatform,
      },
    },
  };
}
