import { ChatSDKError } from '../error/ChatSDKError';

export const validateAndFilterWebSocketQuery = (
  websocketQuery: Record<string | number, unknown> | undefined,
  onError?: (error: Error) => void,
): Record<string, string> => {
  if (!websocketQuery) {
    return {};
  }

  const validWebSocketQuery = Object.entries(websocketQuery).reduce(
    (acc, entry) => validateAndFilterQueryParam(acc, entry, onError),
    {} as Record<string, string>,
  );

  return validWebSocketQuery;
};

const validateAndFilterQueryParam = (
  acc: Record<string, string>,
  [key, value]: [unknown, unknown],
  onError?: (error: Error) => void,
): Record<string, string> => {
  if (
    (typeof key !== 'string' && typeof key !== 'number') ||
    (typeof value !== 'string' && typeof value !== 'number')
  ) {
    onError?.(
      new ChatSDKError(
        `Invalid WebSocket query parameter: key "${key}" and value "${value}" must be strings or numbers.`,
      ),
    );

    return acc;
  }

  // Convert key to string
  const queryKey = String(key);

  // check for empty key
  if (queryKey.trim() === '') {
    onError?.(
      new ChatSDKError(
        `Invalid WebSocket query parameter: key "${queryKey}" must not be an empty string.`,
      ),
    );

    return acc;
  }

  const searchParams = new URLSearchParams();

  try {
    searchParams.append(queryKey, String(value));
    searchParams.toString(); // This will throw error if searchParams is invalid
  } catch (e) {
    onError?.(
      new ChatSDKError(
        `Invalid WebSocket query parameter: key "${queryKey}" and value "${value}" must not be empty strings.`,
      ),
    );

    return acc;
  }

  acc[queryKey] = String(value);

  return acc;
};
