import { SDKVariableStorage } from '../variables/VariableStorage';

import { EnvironmentEndpoints } from './Environments';

export const setEnvironmentEndpoints = (
  endpoints: EnvironmentEndpoints,
): void => {
  assertEndpoint(endpoints.gateway, 'gateway');
  assertEndpoint(endpoints.chat, 'chat');
  assertEndpoint(endpoints.authorize, 'authorize');

  SDKVariableStorage.ENDPOINT_GATEWAY = endpoints.gateway;
  SDKVariableStorage.ENDPOINT_CHAT = endpoints.chat;
  SDKVariableStorage.ENDPOINT_AUTHORIZE = endpoints.authorize;
};

const validateEndpoint = (endpoint: string): boolean => {
  try {
    new URL(endpoint);
  } catch (error) {
    return false;
  }

  return true;
};

const assertEndpoint = (endpoint: string, endpointName: string): void => {
  if (!validateEndpoint(endpoint)) {
    throw new Error(
      `Invalid ${endpointName} endpoint: ${endpoint}. Must be a valid URL.`,
    );
  }
};
