import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { AwsInputEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsInputEvent';
import { ContactNumber } from '@brandembassy/ts-types-platform/dist/contact/ContactNumber';

import { EventPayloadData } from '../sockets/EventPayload';

export const createSendTranscriptPayloadData = (
  contactNumber: ContactNumber,
  email: string,
): EventPayloadData<AwsInputEventData> => ({
  eventType: AwsInputEventType.SEND_TRANSCRIPT,
  data: {
    consumerContact: {
      id: contactNumber,
    },
    consumerRecipients: [
      {
        idOnExternalPlatform: email,
      },
    ],
  },
});
