import { AwsInputEventType } from '@brandembassy/ts-types-chat/dist/awsEvents/AwsEventType';
import { LeaveGroupChatEventData } from '@brandembassy/ts-types-chat/dist/awsEvents/LeaveGroupChat';
import { CaseId } from '@brandembassy/ts-types-platform/dist/post/case/CaseId';

import { ChatEventData } from '../event/ChatEvent';
import { sendChatEvent } from '../event/sendChatEvent';
import { EventPayloadData } from '../sockets/EventPayload';
import { WebSocketClient } from '../sockets/WebSocketClient';

export function createLeaveGroupChatPayloadData(
  id: CaseId,
): EventPayloadData<LeaveGroupChatEventData> {
  return {
    eventType: AwsInputEventType.LEAVE_GROUP_CHAT,
    data: {
      contact: {
        id,
      },
    },
  };
}

export async function sendLeaveGroupChatEvent(
  leaveGroupChatPayloadData: EventPayloadData<LeaveGroupChatEventData>,
  wsClient: WebSocketClient | null,
): Promise<ChatEventData> {
  return sendChatEvent(leaveGroupChatPayloadData, wsClient);
}
