import { CaseId } from '@brandembassy/ts-types-platform/dist/post/case/CaseId';

import { SDKVariableStorage } from '../variables/VariableStorage';

export function storeContactId(contactId: CaseId): void {
  const originalThreadData = SDKVariableStorage.THREAD_DATA ?? {};

  SDKVariableStorage.THREAD_DATA = {
    ...originalThreadData,
    contactId,
  };
}
