import { ChatSDKError } from '../error/ChatSDKError';
import { MessageFailedEventData } from '../message/MessageSuccessEventData';

export class AuthorizationError extends ChatSDKError {
  data: MessageFailedEventData['error'] | undefined;

  constructor(
    message: string,
    data: MessageFailedEventData['error'] | undefined,
  ) {
    super(message, data);

    if (data !== undefined) {
      this.message = `${message} because of (${data.errorMessage})`;
    }
  }
}
