import { ChatCustomEvent, ChatEventType } from './ChatEvent';

export interface IChatEventTarget extends EventTarget {
  addEventListener<K extends ChatEventType>(
    type: K,
    listener: (event: ChatCustomEvent) => void,
    options?: boolean | AddEventListenerOptions,
  ): void;
  addEventListener(
    type: string,
    callback: EventListenerOrEventListenerObject | null,
    options?: EventListenerOptions | boolean,
  ): void;

  dispatchEvent(event: ChatCustomEvent): boolean;

  removeEventListener(
    type: string,
    callback: EventListenerOrEventListenerObject | null,
    options?: boolean | EventListenerOptions | undefined,
  ): void;
  removeEventListener<K extends ChatEventType>(
    type: K,
    callback: (event: ChatCustomEvent) => void,
    options?: boolean | EventListenerOptions | undefined,
  ): void;
}

export const ChatEventTarget = EventTarget as {
  new (): IChatEventTarget;
  prototype: IChatEventTarget;
};
